<script>
   /*************************************************************/
   /* PlotYResiduals                                            */
   /* --------------------                                      */
   /* shows plot with y-residuals (scatter, QQ, hist)           */
   /*************************************************************/

   import { Vector } from 'mdatools/arrays';
   import { Lines, Bars, TextLabels } from 'svelte-plots-basic/2d';
   import { mrange, split, count, mids, ppoints, range } from 'mdatools/stat';
   import { qnorm } from 'mdatools/distributions';

   import { LINE_COLORS, LABELS_COLOR, FACE_TCOLORS, MARKERS } from './Defaults.js';
   import PlotSeries from './PlotSeries.svelte';
   import {plsres_get} from '../wasm/dist/pls.js';

   import Axes from './Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();

   // computes number of bins
   function getNBins(iqr, rng, n) {
      if (n < 10) return Math.round(Math.log2(n) + 1);
      const binWidn = 2 * iqr / Math.pow(n, 0.3333333);
      return Math.round(rng / binWidn);
   }

   // constant parameters
   const xLabels = {'p': 'Reference y-values', 'qq': 'Standard normal quantiles, z', 'h': 'Residuals, e'};
   const yLabels = {'p': 'Residuals, e', 'qq': 'Sample quantiles, e', 'h': 'Counts'};

   // get the parameters needed for the plot
   const resobj = $derived(props.res[params.resID]);

   // plot parameters
   const title = $derived(resobj ? 'Residuals for ' + resobj.varlabels[params.resp - 1] + ' (A = ' + props.ncomp + ')' : '');
   const xLabel = $derived(xLabels[params.plotType]);
   const yLabel = $derived(yLabels[params.plotType]);

   // main outcomes
   const yr = $derived(resobj ? resobj.Yref.getcolref(params.resp) : null);
   const yp = $derived(resobj ? plsres_get(resobj, 'Yp', props.ncomp, params.resp) : null);
   const e = $derived(yr && yp ? new Vector(yr.map((v, i) => v - yp[i])) : null);


   // for qq-plots
   const qqx = $derived(e ? e.sort() : null);
   const qqp = $derived(resobj ? ppoints(resobj.nrows) : null);
   const qqz = $derived(qqp ? qnorm(qqp) : null);
   const q1 = $derived(qqx ? qqx.v[Math.round(qqx.length * 0.25)] : null);
   const q3 = $derived(qqx ? qqx.v[Math.round(qqx.length * 0.75)] : null);
   const iqr = $derived(q1 && q3 ? q3 - q1 : null);

   const qqslope = $derived(iqr ? iqr / 1.348 : 1);
   const qqint = $derived(q1 && qqslope ? q1 + qqslope * 0.674 : 0);
   const qqlineY = $derived([-4 * qqslope + qqint, 4 * qqslope + qqint]);
   const qqlineX = [-4, 4];


   // for histograms
   const n = $derived(resobj ? resobj.nrows : 1);
   const rng = $derived(e ? range(e) : null);
   const nbins = $derived( iqr && n && rng? getNBins(iqr, rng[1] - rng[0], n) : 10);
   const bins = $derived(e ? split(e, nbins) : null);
   const counts = $derived(bins ? count(e, bins) : null);
   const mds = $derived(bins ? mids(bins) : null);

   // labels
   const ind = $derived(e ? Array.from(e.sortind().v) : null);
   const labels = $derived(params.plotType === 'p' ? resobj.objlabels : ind.map(v => resobj.objlabels[v - 1]));

   // x-values
   const xValues = $derived.by(() => {
      if (!e) return null;
      if (params.plotType === 'p') return new Vector(yr);
      if (params.plotType === 'qq') return qqz;
      return mds;
   });

   // y-values
   const yValues = $derived.by(() => {
      if (!e) return null;
      if (params.plotType === 'p') return e;
      if (params.plotType === 'qq') return qqx;
      return counts;
   });

   const mainColor = $derived(LINE_COLORS[params.resID]);
   const faceColor = $derived(FACE_TCOLORS[params.resID]);
   const marker = $derived(MARKERS[params.resID]);

   const limX = $derived(xValues ? mrange(xValues) : [0, 1]);
   const limY = $derived(yValues ? mrange(yValues) : [0, 1]);
</script>

{#if xValues && yValues }
<Axes {title} {xLabel}
   {limX}
   {limY}
   {yLabel}
   fileName='plot-yresiduals'
   onclick={callbacks.unselectObject}
   >

   {#if params.plotType === 'qq'}
      <PlotSeries {xValues} {yValues} {mainColor} {faceColor} {marker} plotType="p"/>

      <Lines xValues={qqlineX} yValues={qqlineY} lineColor="#606060" />
      {#if params.showLabels !== 'hide'}
      <TextLabels {xValues} {yValues} {labels} pos={3} textSize={0.85} faceColor={LABELS_COLOR}/>
      {/if}
   {:else if params.plotType === 'h'}
      <Bars  {xValues} {yValues} faceColor={mainColor} lineColor={mainColor} barWidth={0.95}/>
   {:else}
      <PlotSeries {xValues} {yValues} {mainColor} {faceColor} {marker} plotType="p"/>
      <Lines xValues={limX} yValues={[0, 0]} lineColor="#606060"/>
      {#if params.showLabels !== 'hide'}
      <TextLabels {xValues} {yValues} {labels} pos={3} textSize={0.85} faceColor={LABELS_COLOR}/>
      {/if}
   {/if}

</Axes>
{/if}