<script>
   /*************************************************************/
   /* AppContainer for predictions                              */
   /* -------------------------                                 */
   /* app container for applying PLS model to new set           */
   /*************************************************************/

   import { untrack } from 'svelte';
   import { Button, Container } from 'svelte-controls-basic';
   import { plsmodel_get, plsres_get } from '../../../shared/wasm/dist/pls';
   import { replacer } from '../../../shared/utils.js';

   import Outliers from '../../../shared/widgets/Outliers.svelte';
   import Contributions from '../../../shared/components/Contributions.svelte';

   import DataLoader from '../shared/DataLoader.svelte';
   import ModelLoader from '../shared/ModelLoader.svelte';

   import { downloadResults } from '../utils';

   import AppPlots from './AppPlots.svelte';

   // WASM promise and session object from parent app
   let {WASM, backupSession, session = $bindable()} = $props();

   // get part of the session for this tab
   const s = $derived(session['new']);

   // properties for showing contributions
   let selected = $state(-1);
   let contributionIsShown = $state(false);


   // callback function to set object with a give position in plot series
   // as outlier
   function markOutlier(pos) {

      // check that position is valid
      if (pos < 0 || pos > s.newres.nrows - 1) return;

      // find sample ID corresponding to the position
      // this is needed if some of the objects already
      // removed from results as outliers
      const id = s.newres.objindices[pos];

      // add the object to outliers array and reset the model
      if (id >= 0 && !s.outliers.includes(id)) {
         s.outliers.push(id);
         s.newres = null;
      }
   }


   // callback to reset outliers
   function resetOutliers() {
      s.outliers = [];
      s.newres = null;
   }

   // callback to undo outliers
   function undoOutliers() {
      s.outliers.pop();
      s.newres = null;
   }

   // show modal dialog with contributions for selected object
   function showContributions(pos) {
      if (pos < 0) return;
      selected = pos;
      contributionIsShown = true;
   }

   // hide modal dialog with contributions
   function hideContributions() {
      contributionIsShown = false;
      selected = -1;
   }



   // effect to track changes in dataset and models
   $effect(() => {

      // if training set is set null - reset model and all related parameters
      if (s.newset === null) {
         s.outliers = [];
         s.newres = null;
      }

      // if model object is set to null - reset dependent objects
      if (s.model === null) {
         s.newres = null;
         s.newset = null;
      }
   });

   // set ncomp selected to the saved one when model is loaded
   $effect(() => {
      if (s.model) s.ncomp_selected = s.model.ncomp_selected;
   })

   $effect(() => {
      const sessionStr = untrack(() => JSON.stringify(session, replacer));
      backupSession(sessionStr, s.model, s.newres);
   })
</script>

<div class="app-container">

   {#if contributionIsShown}
   <Contributions
      model_getter={plsmodel_get}
      res_getter={plsres_get}
      model={s.model}
      res={{new: s.newres}}
      ncomp={s.ncomp_selected}
      {selected}
      closeModal={hideContributions}
   />
   {/if}

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="model-loader">
         <ModelLoader bind:model={s.model} />
      </div>

      {#if s.model}
      <div class="data-loader">
         <DataLoader
            {WASM}
            bind:dataset={s.newset}
            bind:resobj={s.newres}
            outliers={s.outliers}
            model={s.model}
            respMandatory={false}
         />
      </div>
      {/if}

      {#if s.outliers && s.outliers.length > 0 }
      <div class="outliers-pane">
         <Outliers outliers={s.outliers} dataset={s.newset} {resetOutliers} {undoOutliers} />
      </div>
      {/if}


      {#if s.newres}
      <div class="ncomp-selector">
         <Container label='Download'>
            <div class="buttons">
            <Button onclick={() => downloadResults(s.newres, s.newset, "new", s.ncomp_selected)} text="Res (new)"/>
            </div>
         </Container>
      </div>
      {/if}
   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !s.newres}
         <div class="help-text">
         {#if !s.model}
            <p>
               In this tab you can apply a PLS regression model developed in <strong>Train</strong> tab to
               new set of measurements without known response variable.
            </p>
            <p><strong>Step 1.</strong></p>
            <p>Load the previously developed PLS model from a JSON file.</p>
         {:else if !s.newset}
            <p><strong>Step 2.</strong></p>
            <p>Load your test set. In this case, because dataset should not contain the response variable you
            can load it either from a CSV file or from SPC files.</p>
            <p>If you load data from a <strong>CSV file</strong>, the file structure should be the same as the training/calibration set but
               <strong>without the response values</strong> (only predictors). Here is an example how data structure may look like
               (note, that row labels and header are optional):
            </p>
            <div>
               <table>
                  <thead>
                  <tr>
                     <td>Name</td>
                     <td>x1</td>
                     <td>x2</td>
                     <td>x3</td>
                  </tr>
                  </thead>
                  <tbody>
                  <tr>
                     <td>O1</td>
                     <td>3.8</td>
                     <td>1.3</td>
                     <td>13.6</td>
                  </tr>
                  <tr>
                     <td>O2</td>
                     <td>11.1</td>
                     <td>12.3</td>
                     <td>3.3</td>
                  </tr>
                  <tr>
                     <td>O3</td>
                     <td>1.2</td>
                     <td>2.3</td>
                     <td>4.2</td>
                  </tr>
                  </tbody>
               </table>
               <p>If your model uses spectra as predictors, you can also load spectral data directly
                  from Galactic Industries <strong>SPC files</strong>. The app supports both single spectrum in a file and
                  files with multiple spectra, however only 2-way datasets can be loaded. You can select several
                  SPC files to load (hold Ctrl or Shift when selecting), in this case they will be combined together
                  into the same dataset.

               </p>
            </div>
            <p></p>

         {/if}
         </div>
      {:else}

         {#key s.newres && s.model }
         {#if s.newres && s.model}
         <AppPlots
            {markOutlier}  {showContributions}
            data={{'new': s.newset}}
            res={{'new': s.newres}}
            model={s.model}
            ncomp={s.ncomp_selected}
         />
         {/if}
         {/key}
      {/if}
   </div>
</div>

<style>

   .app-container {
      font-family: Helvetica, Arial, sans-serif;
      font-size: 1rem;
      color: #404040;
      box-sizing: border-box;
      margin: 0 auto;
      padding: 0.35em 0.25em;
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-areas: "controls content";
      grid-template-rows: 100%;
      grid-template-columns: min(310px, 30%) 1fr;
   }

   .app-controls {
      box-sizing: border-box;
      padding: 0.25em 0.5em;
   }

   .app-content {
      box-sizing: border-box;
      padding: 0 1em 0 0.5em;
      padding: 0.25em 0.25em;
      height: 100%;
   }

   .app-controls > div {
      border-radius: 0.25rem;
   }

   .help-text {
      margin-top: 0.25rem;
   }

   .help-text > p, .help-text > div {
      background: #fafafa;
      padding: 0em 1.5em 0.5em 1.5em;
      color: #606060;
      line-height: 1.5em;
      margin: 0 1em 0 0;
   }

   .help-text table {
      border-collapse: collapse;
   }

   .help-text table td{
      padding: 0.25em 0.5em;
      background: #e0f0fa;
      text-align: right;
      border: 1px solid white;
   }

   .help-text table thead tr td{
      font-weight: bold;
      background: #f8f4e0;
   }

   .help-text table td:first-child {
      font-weight: bold;
      background: #f8f4e0;
      padding-right: 1em;
   }


   .help-text > p:first-child {
      border-top-left-radius: 0.25rem;
      border-top-right-radius: 0.25rem;
      padding-top: 1.5em;
   }

   .help-text > p:last-child {
      border-bottom-left-radius: 0.25rem;
      border-bottom-right-radius: 0.25rem;
      padding-bottom: 1.5em;
   }


   @media(min-width:1300px) {
      .app-container {
         grid-template-columns: min(340px, 30%) 1fr;
      }
   }

   @media(min-width:1100px) {
      .app-container {
         grid-template-columns: min(330px, 30%) 1fr;
      }
   }

</style>