import { replacer } from '../../shared/utils';

/**
 * Creates CSV file with PLS result outcomes and download it as a file.
 *
 * @param {Object} res - object with PLS results.
 * @param {Object} dataset - object with dataset used to get the results.
 * @param {string} name - name of the results (e.g. 'cal').
 */
export function downloadResults(res, dataset, name, nCompSelected) {

   let outliers = res.outliers;
   let exclvars = res.exclvars;

   if (!outliers || !Array.isArray(outliers)) {
      outliers = [];
   }


   // local copy for separators
   const sep = dataset.sep;

   // number of rows with/without outliers
   const nAll = dataset.values.nrows;
   const nOut = outliers.length;
   const n = nAll - nOut;

   // number of responses and components
   const nResp = res.nresp;
   const nRespModel = res.nresp_model;
   const nComp = res.ncomp;

   // number of empty columns to add in addition to row name and first value
   const filler = Array(nComp).fill('');

   // vector with component names/labels and values separator
   const compNames = Array(nComp).fill(0).map((v, i) => 'LV' + (i + 1));

   // function for adding component based outcomes for every row
   function addChunk(name, offset) {
      const rowlabels = dataset.objlabels;

      out += '\n';
      out += [name + ': ',  ...filler].join(sep) + sep + '\n';
      out += ['object', ...compNames].join(sep) + sep + '\n';

      // array with current outcome (score, etc) n rows x nComp columns
      const values = res.v.subarray(offset, offset + nComp * n);

      // n - is number of rows in result objects without outliers
      // j - is an iterator along these rows
      // nAll - is a number of rows in original dataset
      // i - is an interator along these rows
      let j = 0;
      for (let i = 0; i < nAll; i++) {

         // outlier
         if (nOut > 0 && outliers.includes(i)) {
            out += [rowlabels[i], ...Array(nComp).fill('-')].join(sep) + sep + '\n';
            continue;
         }

         // regular
         const valuesi = Array(nComp);
         for (let a = 0; a < nComp; a++) {
            valuesi[a] = values[a * n + j];
         }

         out += [rowlabels[i], ...valuesi].join(sep) + sep + '\n';
         j = j + 1;
      }
      out += '\n';
   }

  // function for adding component based outcomes for every row
   function addYP(ny) {
      const rowlabels = dataset.objlabels;
      const offset = 3 * nComp * n + nComp;
      const length = nComp * n * nRespModel;
      const values = res.v.subarray(offset, offset + length);

      // n - is number of rows in result objects without outliers
      // j - is an iterator along these rows
      // nAll - is a number of rows in original dataset
      // i - is an interator along these rows
      let j = 0;
      for (let i = 0; i < nAll; i++) {

         // outlier
         if (nOut > 0 && outliers.includes(i)) {
            out += [rowlabels[i], ...Array(nComp).fill('-')].join(sep) + sep + '\n';
            continue;
         }

         // regular
         const valuesi = Array(nComp);
         for (let a = 0; a < nComp; a++) {
            valuesi[a] = values[a * (n * nRespModel) + (ny - 1) * n + j];
         }

         out += [rowlabels[i], ...valuesi].join(sep) + sep + '\n';
         j = j + 1;
      }
      out += '\n';
   }

   // general information
   let out = '';
   out += ['Number of LVs:', nComp, ...filler].join(sep) + '\n';
   out += ['Optinal number of LVs:', nCompSelected, ...filler].join(sep) + '\n';
   out += ['Center:', res.center, ...filler].join(sep) + '\n';
   out += ['Scale:', res.scale, ...filler].join(sep) + '\n';

   if (outliers.length > 0) {
      out += ['Removed outliers:', outliers.length, ...filler].join(sep) + '\n';
   }

   if (exclvars.length > 0) {
      out += ['Excluded variables:', exclvars.length, ...filler].join(sep) + '\n';
   }

   // variances
   out += '\n';
   if (nResp > 0) {
      out += ['Variances:', '', ...filler].join(sep) + '\n';
      out += ['', ...compNames].join(sep) + sep + '\n';
      out += ['xexpvar:', ...res.stat.xexpvar].join(sep) + sep + '\n';
      out += ['xcumexpvar', ...res.stat.xcumexpvar].join(sep) + sep + '\n';

      out += ['yexpvar:', ...res.stat.yexpvar].join(sep) + sep + '\n';
      out += ['ycumexpvar:', ...res.stat.ycumexpvar].join(sep) + sep + '\n';
      out += '\n';
      out += '\n';

      for (let ny = 1; ny <= nResp; ny++) {
         out += ['Response variable:' + sep + res.varlabels[ny - 1], ...filler].join(sep) + '\n\n';

         out += ['FoMs:', ...compNames].join(sep) + sep + '\n';
         if (res.stat) {
            out += ['RMSE', ...res.stat.rmse.getcolref(ny)].join(sep) + sep + '\n';
            out += ['R2', ...res.stat.r2.getcolref(ny)].join(sep) + sep + '\n';
            out += ['Slope', ...res.stat.slope.getcolref(ny)].join(sep) + sep + '\n';
            out += ['Bias', ...res.stat.bias.getcolref(ny)].join(sep) + sep + '\n';
            out += '\n';
         }

         out += ['Predictions:', ...compNames].join(sep) + sep + '\n';
         addYP(ny)
      }
   } else {
      for (let ny = 1; ny <= nRespModel; ny++) {
         out += ['Response variable:' + sep + res.varlabels[ny - 1], ...filler].join(sep) + '\n\n';
         out += ['Predictions:', ...compNames].join(sep) + sep + '\n';
         addYP(ny)
      }

   }

   // scores and distances
   addChunk('X-scores', 0);
   addChunk('Score distances, h', nComp * n);
   addChunk('Orthogonal distances, q', nComp * n * 2);

   if (res.stat) {
      addChunk('Y-scores', 3 * nComp * n + nComp + n * nComp * nResp);
      addChunk('Orthogonal distances, z', 4 * nComp * n + nComp + n * nComp * nResp);
   }

   // if decimal separator is not ".", replace all "." with the corect separator
   if (dataset.dec !== '.') {
      out = out.replace(/\./g, dataset.dec);
   }

   // add header
   out = ['Data filename:', dataset.fileName, ...filler].join(dataset.sep) + '\n' + out;

   let hdr = ['PLS results (' + name + ')', 'https://mda.tools/pls/', ...filler].join(dataset.sep) + '\n\n';

   // create Blob and start download process
   const blob = new Blob([hdr + out], { type: 'text/csv' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', `pls-results-${name}.csv`);
   a.click()
}


/**
 * Creates JSON with model object, stringify it and download as a file.
 *
 * @param {Object} model - object with PLS model
 */
export function downloadModel(model, ncomp_selected, prepmodel) {

   model['ncomp_selected'] = ncomp_selected;
   model['prep'] = prepmodel;
   const blob = new Blob([JSON.stringify(model, replacer)], { type: 'application/json' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', 'pls-model.json');
   a.click()
}
