<script>
   /*************************************************************/
   /* AppContainer for test set                                 */
   /* -------------------------                                 */
   /* app container for testing a PLS model                     */
   /*************************************************************/

   import { untrack } from 'svelte';
   import { Button, Container, Range } from 'svelte-controls-basic';
   import { plsmodel_get, plsres_get } from '../../../shared/wasm/dist/pls';
   import { replacer } from '../../../shared/utils.js';

   import Outliers from '../../../shared/widgets/Outliers.svelte';
   import Contributions from '../../../shared/components/Contributions.svelte';

   import DataLoader from '../shared/DataLoader.svelte';
   import ModelLoader from '../shared/ModelLoader.svelte';
   import ResultTable from '../shared/ResultTable.svelte';
   import { downloadResults, downloadModel } from '../utils';

   import AppPlots from './AppPlots.svelte';

   // WASM promise and session object from parent app
   let {WASM, backupSession, session = $bindable()} = $props();

   // get part of the session for this tab
   const s = $derived(session['test']);

   // properties for showing contributions
   let selected = $state(-1);
   let contributionIsShown = $state(false);


   // callback function to set object with a give position in plot series
   // as outlier
   function markOutlier(pos) {

      // check that position is valid
      if (pos < 0 || pos > s.testres.nrows - 1) return;

      // find sample ID corresponding to the position
      // this is needed if some of the objects already
      // removed from results as outliers
      const id = s.testres.objindices[pos];
      // add the object to outliers array and reset the model
      if (id >= 0 && !s.outliers.includes(id)) {
         s.outliers.push(id);
         s.testres = null;
      }
   }


   // callback to reset outliers
   function resetOutliers() {
      s.outliers = [];
      s.testres = null;
   }

   // callback to undo outliers
   function undoOutliers() {
      s.outliers.pop();
      s.testres = null;
   }

   // show modal dialog with contributions for selected object
   function showContributions(pos) {
      if (pos < 0) return;
      selected = pos;
      contributionIsShown = true;
   }

   // hide modal dialog with contributions
   function hideContributions() {
      contributionIsShown = false;
      selected = -1;
   }

   // effect to track changes in dataset and models
   $effect(() => {

      // if training set is set null - reset model and all related parameters
      if (s.testset === null) {
         s.outliers = [];
         s.testres = null;
      }

      // if model object is set to null - reset dependent objects
      if (s.model === null) {
         s.testres = null;
         s.testset = null;
      }
   });

   // set ncomp selected to the saved one when model is loaded
   $effect(() => {
      if (s.model) s.ncomp_selected = s.model.ncomp_selected;
   });

   // trigger session backup
   $effect(() => {
      const sessionStr = untrack(() => JSON.stringify(session, replacer));
      backupSession(sessionStr, s.model, s.testres);
   });
</script>

<div class="app-container">
   {#if contributionIsShown}
   <Contributions
      model_getter={plsmodel_get}
      res_getter={plsres_get}
      model={s.model}
      res={{test: s.testres}}
      ncomp={s.ncomp_selected}
      {selected}
      closeModal={hideContributions}
   />
   {/if}

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="model-loader">
         <ModelLoader bind:model={s.model} />
      </div>

      {#if s.model}
      <div class="data-loader">
         <DataLoader
            {WASM}
            bind:dataset={s.testset}
            bind:resobj={s.testres}
            outliers={s.outliers}
            model={s.model}
            respMandatory={true}
         />
      </div>

      {#if s.outliers && s.outliers.length > 0 }
      <div class="outliers-pane">
         <Outliers outliers={s.outliers} dataset={s.testset} {resetOutliers} {undoOutliers} />
      </div>
      {/if}


      {#if s.model && s.testres}
      <div class="ncomp-selector">
         <Container name="ncomp" label="# of LVs">
            <Range bind:value={s.ncomp_selected} min={1} max={s.model.ncomp} step={1} decNum={0} />
         </Container>
         <Container label='Download'>
            <div class="buttons">
            <Button onclick={() => downloadResults(s.testres, s.testset, "test", s.ncomp_selected)} text="Res (test)"/>
            <Button onclick={() => downloadModel(s.model, s.ncomp_selected)} text="Model" />
            </div>
         </Container>
      </div>
      <div class="result-stats">
         <ResultTable res={{test: s.testres}} ncomp={s.ncomp_selected} />
      </div>
      {/if}
   {/if}

   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !s.testres}
         <div class="help-text">
         {#if !s.model}
            <p>
               In this tab you can test a PLS regression model developed in <strong>Train</strong> tab by
               applying it to a test set, which contains both predictors and responses.
            </p>
            <p><strong>Step 1.</strong></p>
            <p>Load the previously developed PLS model from a JSON file.</p>
         {:else if !s.testset}
            <p><strong>Step 2.</strong></p>
            <p>Load your test set from a CSV file. The file structure should be the same as the training/calibration set.
               The response variable is mandatory. Here is an example how data structure may look like (note, that row labels
               and header are optional):
            </p>
            <div>
               <table>
                  <thead>
                  <tr>
                     <td>Name</td>
                     <td>y</td>
                     <td>x1</td>
                     <td>x2</td>
                     <td>x3</td>
                  </tr>
                  </thead>
                  <tbody>
                  <tr>
                     <td>O1</td>
                     <td>96.1</td>
                     <td>3.8</td>
                     <td>1.3</td>
                     <td>13.6</td>
                  </tr>
                  <tr>
                     <td>O2</td>
                     <td>88.9</td>
                     <td>11.1</td>
                     <td>12.3</td>
                     <td>3.3</td>
                  </tr>
                  <tr>
                     <td>O3</td>
                     <td>56.1</td>
                     <td>1.2</td>
                     <td>2.3</td>
                     <td>4.2</td>
                  </tr>
                  </tbody>
               </table>
            </div>
            <p></p>

         {/if}
         </div>
      {:else}

         {#key s.testres && s.model }
         {#if s.testres && s.model}
         <AppPlots
            {markOutlier}  {showContributions}
            data={{'test': s.testset}}
            res={{'test': s.testres}}
            model={s.model}
            ncomp={s.ncomp_selected}
         />
         {/if}
         {/key}
      {/if}
   </div>
</div>

