<script>
   /*************************************************************/
   /* AppContainer for training                                 */
   /* ---------------------------                               */
   /* app container for training a PLS model                    */
   /*************************************************************/

   import { untrack } from 'svelte';
   import { Button, Container, Range } from 'svelte-controls-basic';
   import { plsmodel_get, plsres_get } from '../../../shared/wasm/dist/pls';
   import { replacer } from '../../../shared/utils.js';
   import { downloadResults, downloadModel } from '../utils';

   import Outliers from '../../../shared/widgets/Outliers.svelte';
   import Exclvars from '../../../shared/widgets/Exclvars.svelte';
   import Contributions from '../../../shared/components/Contributions.svelte';
   import PrepWidget from '../../../prep/src/shared/PrepWidget.svelte';

   import ResultTable from '../shared/ResultTable.svelte';
   import ModelCalculator from '../shared/ModelCalculator.svelte';
   import PCVCalculator from '../shared/PCVCalculator.svelte';
   import DataLoader from '../shared/DataLoader.svelte';

   import AppPlots from './AppPlots.svelte';

   // WASM promise and session object from parent app
   let {WASM, backupSession, session = $bindable()} = $props();

   // get part of the session for this tab
   const s = $derived(session['train']);

   // properties for showing contributions
   let selected = $state(-1);
   let contributionIsShown = $state(false);

   // exclude variables whose positions provided in array "pos"
   function excludeVars(pos) {

      // check that array is not empty
      if (pos.length < 1) return;

      // find variables ID corresponding to the positions
      // this is needed if some of the variables have been removed already
      // 's.model.nresp' is added as varindices contains both indices or responses and predictors
      const exclvars = pos.map(i => s.model.varindices[i + s.model.nresp]);

      if (exclvars.length > 0) {

         // make sure ther are no duplcates or already removed indices
         const exclvars_old_set = new Set(s.exclvars);
         const exclvars_filtered = [...new Set(exclvars)].filter(item => !exclvars_old_set.has(item));

         // add new indices to global array and reset all outcomes and model
         s.exclvars = s.exclvars.concat(exclvars_filtered);
         s.model = null;
         s.pvres = null;
         s.calres = null;
         s.pvset = null;
         s.ncomp_selected = 1;
      }
   }

   // reset all excluded variables, outcomes and model
   function resetExclvars() {
      s.exclvars = [];
      s.model = null;
      s.pvres = null;
      s.calres = null;
      s.pvset = null;
      s.ncomp_selected = 1;
   }

   // mark object with index "pos" as outlier
   function markOutlier(pos) {

      // check that position is valid
      if (pos < 0 || pos > s.calres.nrows - 1) return;

      // find sample ID corresponding to the position
      // this is needed if some of the objects already
      // removed from results as outliers
      const id = s.calres.objindices[pos];

      // add the object to outliers array and reset the model
      if (id >= 0 && !s.outliers.includes(id)) {
         s.outliers.push(id);
         s.model = null;
         s.pvres = null;
         s.calres = null;
         s.calsetp = null;
         s.prepmodel = null;
         s.pvset = null;
         s.ncomp_selected = 1;
      }
   }

   // reset list of outliers, outcomes and the model
   function resetOutliers() {
      s.outliers = [];
      s.model = null;
      s.pvres = null;
      s.calres = null;
      s.calsetp = null;
      s.prepmodel = null;
      s.pvset = null;
      s.ncomp_selected = 1;
   }

   // remove last added outlier from the list
   function undoOutliers() {
      s.outliers.pop();
      s.model = null;
      s.pvres = null;
      s.calres = null;
      s.calsetp = null;
      s.prepmodel = null;
      s.pvset = null;
      s.ncomp_selected = 1;
   }

   // shows a modal dialog with contributions plot for object with index "pos"
   function showContributions(pos, resID) {
      if (pos < 0) return;
      selected = pos;
      contributionIsShown = true;
   }

   // hides the contribiution plot dialog
   function hideContributions() {
      contributionIsShown = false;
      selected = -1;
   }


   // if training set is set null - reset model and all related parameters
   $effect(() => {
      if (s.calset === null) {
         s.pvres = null;
         s.calres = null;
         s.pvset = null;
         s.calsetp = null;
         s.prepmodel = null;
         s.prepobj = [];
         s.prepType = null
         s.outliers = [];
         s.exclvars = [];
         s.ncomp_selected = 1;
         s.model = null;
      }
   });

   // if model object is set to null - reset dependent objects
   $effect(() => {
      if (s.model === null) {
         s.calres = null;
         s.pvres = null;
         s.pvset = null;
      }
   });

   // no preprocessing model - set calsetp ot null
   $effect(() => {
      if (!s.prepmodel) {
         s.calres = null;
         s.calsetp = null;
         s.exclvars = [];
      }
   });

   // no calsetp - no model
   $effect(() => {
      if (!s.calsetp) {
         s.model = null;
         s.calres = null;
         s.pvres = null;
      }
   });

   // reactive effect to compute initial value for the optimal number of components
   $effect(() => {
      if (s.model && s.model.ncomp && s.pvres && s.pvres.stat) {
         const rmse = s.pvres.stat.rmse.getcolref(1);
         const Q2 = rmse.map((v, i) => i > 0 ? 1 - v*v / (rmse[i-1]**2) : 1);
         const ncomp = Q2.findIndex(v => v <= 0.0975);
         s.ncomp_selected = ncomp < 0 ? s.model.ncomp : ncomp;
      }
   });

   // reactive effect for backing up the session
   $effect(() => {
      const sessionStr = untrack(() => JSON.stringify(session, replacer));
      backupSession(sessionStr, s.model, s.calset, s.calsetp, s.pvres);
   });
</script>

<div class="app-container">

   <!-- modal dialog with contribution plot -->
   {#if contributionIsShown}
   <Contributions
      model_getter={plsmodel_get}
      res_getter={plsres_get}
      model={s.model}
      res={{'cal': s.calres, 'pv': s.pvres}}
      ncomp={s.ncomp_selected}
      {selected}
      closeModal={hideContributions}
   />
   {/if}

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="data-loader">
         <DataLoader bind:dataset={s.calset} respMandatory={true} />
      </div>

      {#if s.outliers && s.outliers.length > 0 }
      <div class="outliers-pane">
         <Outliers outliers={s.outliers} dataset={s.calset} {resetOutliers} {undoOutliers} />
      </div>
      {/if}

      {#if s.calset }
         <PrepWidget
            {WASM}
            rawdata={s.calset}
            outliers={s.outliers}
            bind:prepType={s.prepType}
            bind:prepobj={s.prepobj}
            bind:prepdata={s.calsetp}
            bind:prepmeta={s.prepmeta}
            bind:prepmodel={s.prepmodel}
         />
      {/if}

      {#if s.exclvars && s.exclvars.length > 0 }
      <div class="exclvars-pane">
         <Exclvars exclvars={s.exclvars} dataset={s.calset} {resetExclvars} />
      </div>
      {/if}

      {#if s.calsetp }
      <div class="model-calculator">
         <ModelCalculator
            {WASM}
            dataset={s.calsetp}
            outliers={s.outliers}
            exclvars={s.exclvars}
            bind:model={s.model}
            bind:resobj={s.calres}
         />
      </div>
      {/if}

      {#if s.calsetp && s.model }
      <div class="pcv-calculator">
         <PCVCalculator
            {WASM}
            calset={s.calsetp}
            outliers={s.outliers}
            model={s.model}
            calres={s.calres}
            bind:pvset={s.pvset}
            bind:pvres={s.pvres}
            bind:pvscalars={s.pvscalars}
         />
      </div>
      {/if}


      {#if s.model && s.model.ncomp && s.calres && s.pvres}
      <div class="ncomp-selector">
         <Container name="ncomp" label="# of LVs">
            <Range bind:value={s.ncomp_selected} min={1} max={s.model.ncomp} step={1} decNum={0} />
         </Container>
         <Container label='Download'>
            <div class="buttons">
            <Button onclick={() => downloadResults(s.calres, s.calset, "train", s.ncomp_selected)} text="Res (cal)"/>
            <Button onclick={() => downloadResults(s.pvres, s.pvset, "PV", s.ncomp_selected)} text="Res (pv)" />
            <Button onclick={() => downloadModel(s.model, s.ncomp_selected, s.prepmodel)} text="Model" />
            </div>
         </Container>
      </div>
      <div class="result-stats">
         <ResultTable res={{cal: s.calres, pv: s.pvres}} ncomp={s.ncomp_selected} />
      </div>
      {/if}
   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !s.calres || !s.pvres || !s.model}
         <div class="help-text">
         {#if !s.calset}
            <p>
               This tab lets you train a PLS regression model and optimize it using Procrustes cross-validation.
               After that you can save the model to a file, apply it to a test set (tab <strong>Test</strong>)
               or to a new set of samples whose response us unknown (tab <strong>Predict</strong>).
            </p>
            <p><strong>Step 1.</strong></p>
            <p>Load your training/calibration set from a CSV file. The file should contain data as numeric values delimited
               by comma (with dot as decimal separator) or by semicolon (with comma as decimal separator). Rows
               correspond to observations or objects, columns  to variables (both response and predictors).
            </p>
            <p>
               First row in the CSV file can contain variable labels or values (<strong>header</strong>). It is assumed
               that variable lables are text (or have text parts), e.g. <em>"Na"</em>, <em>"OD260"</em>, etc. If x-variables
               are signals, e.g. spectra, this part of header can content numeric values, e.g. wavelength or wavenumbers.
               If this is the case, select <em>values</em> option for the header. This will let you to specify
               name of the values (e.g. <em>Wavelength</em>) and the units (e.g. <em>nm</em>) if necessary.
            </p>
            <p>
               First column in the file can contain labels for the objects/observations (<strong>row labels</strong>)  if so, just set
               the corresponding options after selecting the file.
            </p>
            <p>
               The response variable (<em>Y</em>) should be located right after the column with object/row labels. If there are no
               object labels, the response variable is expected to be in the first column. <strong>Response variable is mandatory
               in the training set and in this version of app only single response is supported (PLS1).</strong>
            </p>
            <p>
               Here is an example how data structure may look like (note, that row labels and header are optional):
            </p>
            <div>
               <table>
                  <thead>
                  <tr>
                     <td>Name</td>
                     <td>y</td>
                     <td>x1</td>
                     <td>x2</td>
                     <td>x3</td>
                  </tr>
                  </thead>
                  <tbody>
                  <tr>
                     <td>O1</td>
                     <td>96.1</td>
                     <td>3.8</td>
                     <td>1.3</td>
                     <td>13.6</td>
                  </tr>
                  <tr>
                     <td>O2</td>
                     <td>88.9</td>
                     <td>11.1</td>
                     <td>12.3</td>
                     <td>3.3</td>
                  </tr>
                  <tr>
                     <td>O3</td>
                     <td>56.1</td>
                     <td>1.2</td>
                     <td>2.3</td>
                     <td>4.2</td>
                  </tr>
                  </tbody>
               </table>
            </div>
            <p></p>
         {:else}
            {#if s.calset && !s.prepmodel }
            <p><strong>Step 2</strong></p>
            <p>
               Next step is to decide how to preprocess the data. You can either skip preprocessing (in the model settings you
               will be able to apply centering and scaling), create the chain of preprocessing methods in special constructor
               (use <em>Create</em> to load the constructor) or load already built preprocessing model from a file. Make your choice.
            </p>
            {/if}

            {#if s.prepmodel && !s.model}
            <p><strong>Step 3</strong></p>
            <p>
               Now you can create a global PLS model for the training set. Use more components/latent variables (LVs) than
               you expect to be optimal. You will have possibility to select the optimal number of components later.
            </p>
            {/if}

            {#if s.model && !s.pvset}
            <p><strong>Step 4</strong></p>
            <p>
               Now set necessary parameters for Procrustes cross-validation and click button to compute the PV-set,
               which will help you to optimize PLS model parameters. The parameters are the same as for conventional
               cross-validation: type of splits and number of segments. It is recommended to use fewer number of
               segments (e.g. 3, 4 or 5).
            </p>
            <p>
               After this step PLS model will be created automatically and you will be able to explore it and tune
               its parameters.
            </p>
            {/if}

         {/if}
         </div>
      {:else}

         {#key s.pvres && s.model }
         {#if s.calres && s.pvres && s.model && s.calset && s.pvset}
         <AppPlots
            {markOutlier} {showContributions} {excludeVars}
            data={{'cal': s.calset, 'pv': s.pvset}}
            res={{'cal': s.calres, 'pv': s.pvres}}
            scalars={s.pvscalars}
            model={s.model}
            ncomp={s.ncomp_selected}
         />
         {/if}
         {/key}
      {/if}
   </div>
</div>

