<script>
   /*************************************************************/
   /* PrepWidget                                                */
   /* -------------------------                                 */
   /* preprocssing widget for any app                           */
   /*************************************************************/

   import { Button, ButtonCancel, ButtonSettings, Container } from 'svelte-controls-basic';
   import { prep_apply, prep_fit, prep_trim } from '../../../shared/wasm/dist/prep.js';
   import { cloneFullData } from '../utils.js';


   import Modal from '../../../shared/components/Modal.svelte';
   import ModelLoader from './ModelLoader.svelte';
   import PrepModal from './PrepModal.svelte';
   import PrepInfo from './PrepInfo.svelte';

   // WASM promise and session object from parent app
   let {
      WASM,
      rawdata,
      outliers,
      prepType = $bindable(),
      prepobj = $bindable(),
      prepdata = $bindable(),
      prepmodel = $bindable(),
      prepmeta = $bindable(),
   } = $props();

   // internal state of the widget
   let dialogOpen = $state(false);
   let left = $state(0);
   let right = $state(rawdata ? rawdata.values.ncols - rawdata.nresp : 0);

   let status = $state('neutral');
   let errMsg = $state('');
   let promise = $state();


   /****************************/
   /* Helper functions         */
   /****************************/

   // fit preprocessing model using prepobject directives.
   async function prepFit(rawdata, prepobj, outliers) {
      if (!prepobj || !rawdata || prepobj.length === 0) return;
      promise = WASM()
         .then(Module => {
            status = 'applying';
            const m = prep_fit(Module, rawdata.values, rawdata.nresp, prepobj, false, outliers);
            m.info = prepmeta;
            prepmodel = m;
         })
         .catch(e =>{
            // something went wrong
            prepmodel = null;
            console.error(e);
            status = 'error';
            errMsg = e;
         });
   }

   // applies current preprocessing sequence to full dataset.
   async function prepApply(rawdata, prepmodel) {

      // no model - no preprocessed data
      if (!prepmodel || typeof prepmodel !== 'object') {
         prepdata = null;
         return;
      }

      // preprocessing skipped - copy data
      if (Object.keys(prepmodel).length === 0) {
         prepdata = rawdata;
         return;
      }

      promise = WASM()
         .then(Module => {
            status = 'applying';
            const v = prep_apply(Module, rawdata.values, rawdata.nresp, prepmodel, false, false);

            // set global variables and status of the component
            const d = cloneFullData($state.snapshot(rawdata), v);
            prepdata = prep_trim(d, prepmodel);
         })
         .catch(e =>{
            // something went wrong
            prepdata = null;
            console.error(e);
            status = 'error';
            errMsg = e;
         });
   }

   // reset preprocessing settings
   function reset() {
      prepType = null;
      prepdata = null;
      prepmodel = null;
      prepobj = [];
      setLeft(0);
      setRight(rawdata.values.ncols - rawdata.nresp);
   }

   // closes dialog and fits preprocessing model
   function closeDialog() {
      if (prepobj && prepobj.length > 0) {
         prepFit(rawdata, prepobj, outliers);
      } else {
         prepType = null;
         prepmodel = null;
      }
      dialogOpen = false;
   }

   // opens dialog to create or edit preprocessing model
   function openDialog() {
      prepType = 'create';
      prepdata = null;
      prepmodel = null;
      dialogOpen = true;
   }

   // skips preprocessing
   function skip() {
      prepType = 'skip';
      prepmodel = {};
   }

   // reads preprocessing model from JSON file
   function readFile (e) {
      const reader = new FileReader();
      reader.onload = () => { setModel(reader.result) };
      reader.readAsText(modelFile);
   }


   /****************************/
   /* Effects                  */
   /****************************/

   // if model or rawdata has changed - reapply the preprocessing
   $effect(() => {
      if (prepmodel, rawdata) {
         prepApply(rawdata, prepmodel);
      }
   });

   // if outliers array has changed reset the preprocessing model
   $effect(() => {
      if (prepType === 'create' && prepobj && !prepmodel) {
         prepFit(rawdata, prepobj, outliers);
      }
   });

   // if prepType is 'skip' set model object to empty
   $effect(() => {
      if (prepType === 'skip' && prepdata === null) {
         prepmodel = {};
         prepobj = [];
         prepdata = rawdata;
      }
   });

</script>

<div class="prep-widget">
   {#if prepmodel && prepdata && prepmodel.ml}
   <Container name="info">
      {#if !dialogOpen}
      <p class="info-text">
         <span style="color:#a0a0a0;">Preprocessing: </span>
         <PrepInfo {prepmodel} />
      </p>
      {/if}

      {#if prepType === 'create'}
         <ButtonSettings onclick={openDialog} />
      {/if}
      <ButtonCancel onclick={reset} />

   </Container>
   {:else if prepType === 'skip'}
   <Container name="prep-info">
      <p class="info-text">
         <span style="color:#a0a0a0;">Preprocessing: </span> skipped
      </p>
      <div>
      <ButtonCancel onclick={reset} />
      </div>
   </Container>
   {:else if !prepType}
   <Container label="Preprocessing" name="prep-container">
      <Button text="Skip" onclick={skip}/>
      <Button text="Load" onclick={() => prepType = 'file'}/>
      <Button text="Create" onclick={openDialog}/>
   </Container>
   {:else if prepType === 'file'}
      <Container>
         <p><strong>Preprocessing model</strong></p>
      </Container>
      <ModelLoader bind:model={prepmodel} bind:prepobj={prepobj} bind:left={left} bind:right={right}/>
   {/if}
</div>

{#if dialogOpen}
<Modal closeModal={closeDialog} isDark={true}>
   <PrepModal
      {WASM}
      {rawdata}
      {outliers}
      bind:prepobj={prepobj}
      bind:left={left}
      bind:right={right}
      bind:meta={prepmeta}
      finalize={closeDialog}
   />
</Modal>
{/if}


<!-- error message -->
{#if status === 'error' || errMsg}
<Container status="error">
   <p>{errMsg}</p>
</Container>
{/if}

<style>
   :global(.prep-container > button){
      margin-left: 0.25em;
   }

</style>