<script>
   /*************************************************************/
   /* ResultsTable                                              */
   /* -------------------------                                 */
   /* shows table with PLS resobjults for new datset             */
   /*************************************************************/

   let { res, ncomp } = $props();

   const nresp = 1;
   const statsv = ['xcumexpvar', 'ycumexpvar'];
   const statsm = ['rmse', 'r2', 'slope', 'bias'];
   let out = $derived.by(() => {
      if (!res) return null;
      if (ncomp < 0) return null;

      const resnames = Object.keys(res);
      const nres = resnames.length;

      const header = ['', ...resnames];
      const first_row = ['#LVs', ...Array(nres).fill(ncomp)];
      let   rows = [first_row];
      for (let stat of statsv) {
         let row = [stat];
         for (let i = 0; i < nres; i++) {
            row.push(res[resnames[i]].stat[stat][ncomp - 1].toFixed(3));
         }
         rows.push(row);
      }
      for (let stat of statsm) {
         let row = [stat];
         for (let i = 0; i < nres; i++) {
            let f = 3;
            const v = res[resnames[i]].stat[stat].getcolref(nresp)[ncomp - 1];
            if (stat === 'rmse') {
               f = v < 1 ? 3 : (v > 100 ? 1 : 2);
            }
            row.push(v.toFixed(f));
         }
         rows.push(row);
      }
      return {header, rows};
   });
</script>


{#if out}
<table>
   <thead>
   <tr>
      {#each out.header as v}
      <th>{v}</th>
      {/each}
   </tr>
   </thead>
   <tbody>
   {#each out.rows as row}
   <tr>
      {#each row as v}
      <td>{v}</td>
      {/each}
   </tr>
   {/each}
   </tbody>
   </table>
{/if}

<style>
   table {
      width: 100%;
      border-collapse: collapse;
   }

   tr {
      width: 100%;
   }

   th {
      border-bottom: 1px solid #a0a0a0;
   }

   th, td {
      text-align: right;
      padding: 0.2em 0.5em 0.2em 0.1em;
      color: #404040;
      margin: 0;
   }

   td:first-of-type {
      width: 25%;
   }
</style>