<script>
   /*************************************************************/
   /* AppPlots component for training app                       */
   /* -----------------------------------                       */
   /* defines plot components, controls and parameters          */
   /*************************************************************/

   import { getDefaults } from 'svelte-controls-basic';
   import { Container, Button } from 'svelte-controls-basic';

   // global plot router
   import PlotsRouter from '../../../shared/plots/PlotsRouter.svelte';

   // import plot components
   import PlotVariance from '../../../shared/plots/PlotPLSVariance.svelte';
   import PlotFoM from '../../../shared/plots/PlotFoM.svelte';
   import PlotPredictions from '../../../shared/plots/PlotPredictions.svelte';
   import PlotScores from '../../../shared/plots/PlotPLSScores.svelte';
   import PlotYResiduals from '../../../shared/plots/PlotYResiduals.svelte';

   import PlotLoadings from '../../../shared/plots/PlotPLSLoadings.svelte';
   import PlotDistance from '../../../shared/plots/PlotPLSDistance.svelte';
   import PlotRegcoeffs from '../../../shared/plots/PlotRegcoeffs.svelte';
   import PlotVarsel from '../../../shared/plots/PlotPLSVarsel.svelte';
   import PlotPVScalars from '../../../shared/plots/PlotPVScalars.svelte';

   // import plot controls components
   import { getPlotPLSVarianceControls, getPlotFoMControls, getPlotPredictionsControls,
      getPlotPLSScoresControls, getPlotPLSLoadingsControls, getPlotPLSDistanceControls,
      getPlotRegcoeffsControls, getPlotPVScalarsControls, getPlotPLSVarselControls,
      getPlotYResidualsControls
   } from '../../../shared/plots/Controls.svelte.js';

   let {
      res,
      model,
      data,
      ncomp,
      scalars,
      markOutlier,
      excludeVars,
      showContributions
   } = $props();


   const resIDs = Object.keys(res);
   const nresp = res[resIDs[0]].nresp;

   // JSON with plot components (scope is needed for providing correct properties)
   const plots = $derived({
      // objects
      pred: {label: 'Predictions', scope: 'objects', component: PlotPredictions, buttons: buttons1},
      scores: {label: 'Scores', scope: 'objects', component: PlotScores, buttons: buttons1},
      distance: {label: 'Distances', scope: 'objects', component: PlotDistance, buttons: buttons1},
      yresiduals: {label: 'Residuals', scope: 'stats', component: PlotYResiduals},

      // variables
      loadings: {label: 'X-loadings and weights', scope: 'stats', component: PlotLoadings},
      regcoeffs: {label: 'Regression coefficients', scope: 'stats', component: PlotRegcoeffs},
      varsel: {label: 'Variable importance', scope: 'variables', component: PlotVarsel, buttons: buttons2},

      // statistics
      fom: {label: 'Figures of merit', scope: 'stats', component: PlotFoM},
      variance: {label: 'Variance', scope: 'stats', component: PlotVariance},
      scalars: {label: 'PV scalars', scope: 'stats', component: PlotPVScalars}
   })

   // JSON with plots controls (as Widget properties)
   const controls = $state({
      pred: getPlotPredictionsControls(res, nresp),
      scores: getPlotPLSScoresControls(res, nresp),
      distance: getPlotPLSDistanceControls(res),
      yresiduals: getPlotYResidualsControls(res, nresp),
      loadings: getPlotPLSLoadingsControls(model, ncomp),
      regcoeffs: getPlotRegcoeffsControls(nresp),
      varsel: getPlotPLSVarselControls(nresp),
      fom: getPlotFoMControls(res, nresp),
      variance: getPlotPLSVarianceControls(res),
      scalars: getPlotPVScalarsControls(res),
   })

   // JSON with currently selected plots parameters
   let params = $state({
      pred: getDefaults(controls.pred),
      scores: getDefaults(controls.scores),
      distance: getDefaults(controls.distance),
      yresiduals: getDefaults(controls.yresiduals),
      loadings: getDefaults(controls.loadings),
      regcoeffs: getDefaults(controls.regcoeffs),
      varsel: getDefaults(controls.varsel),
      fom: getDefaults(controls.fom),
      variance: getDefaults(controls.variance),
      scalars: getDefaults(controls.scalars)
   });


   // interactive effects for scores plot
   $effect(() => {
      if (params.scores.varSet === 'XY') {
         controls.scores.comp1.hidden = true;
         controls.scores.comp2.label = 'LV';
      } else {
         controls.scores.comp2.label = 'LV (y-axis)';
         controls.scores.comp1.hidden = false;
      }
   });

   // interactive effects for loadings plot
   $effect(() => {
      if (params.loadings.plotType !== 'p') {
         controls.loadings.comp1.hidden = true;
      } else {
         controls.loadings.comp1.hidden = false;
      }
   });

   // interactive effects for predictions plot
   $effect(() => {
      if (params.pred.resID === 'both') {
         controls.pred.showStat.hidden = true;
      } else {
         controls.pred.showStat.hidden = false;
      }
   });

   // interactive effects for y-resiudals plot
   $effect(() => {
      if (params.yresiduals.plotType === 'h') {
         controls.yresiduals.showLabels.hidden = true;
      } else {
         controls.yresiduals.showLabels.hidden = false;
      }
   });


   // combine main elements to be shared with all plots together
   const props = $derived({res, model, data, ncomp, scalars});

   // combine all callbacks together
   const callbacks = {markOutlier};
</script>

{#snippet buttons1(sel)}
   <Container label="Selected">
      <Button onclick={() => showContributions(sel)} text="plot" />
      {#if (sel >= 0)}
      <Button onclick={() => markOutlier(sel)} text="remove" />
      {/if}
   </Container>
{/snippet}

{#snippet buttons2(sel)}
   <Container label="Selected">
      {#if (res.cal.npred - sel.length >= 2)}
      <Button onclick={() => excludeVars(sel)} text="exclude {sel.length} variables" />
      {:else}
      <p>can not exclude</p>
      {/if}
   </Container>
{/snippet}

<PlotsRouter
   {plots}
   {props}
   {controls}
   {callbacks}
   bind:params={params}
/>

<style>
   :global(.plot-controls-area > .plot-selector-fom),
   :global(.plot-controls-area > .plot-selector-loadings) {
      margin-top: 1em;
   }
</style>