<script>
   /*************************************************************/
   /* PlotDistance for PCA results                              */
   /* ----------------------------                              */
   /* shows plot with score and orthogonal distances            */
   /* and acceptance areas  (both for objects and variables)    */
   /*************************************************************/

   import { Lines, Legend } from 'svelte-plots-basic/2d';
   import {plsmodel_get, plsres_get } from '../wasm/dist/pls.js';

   import { FACE_TCOLORS, LINE_COLORS, MARKERS, MAX_LABEL_SYMBOLS, getResLegend, getDistanceAxisLabel,
      getDistanceCoords, getDistanceLimitCoordinates } from './Defaults.js';
   import PlotSeries from './PlotSeries.svelte';
   import Axes from './Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();

   // get the parameters needed for the plot
   let model = $derived(props && props.model ? props.model : null);
   let res = $derived(props && props.res ? props.res : null);
   let resIDs = $derived(res ? Object.keys(res) : null);
   let plotResIDs = $derived(params && params.resID && resIDs ? (params.resID === 'both' ? resIDs : [params.resID]) : null);

   // constant plot parameters
   const LINE_COLOR = '#909090';    // color of the limits lines


   // get Nu and u0 for specific type of limits
   const N = $derived.by(() => {
      if (!model) return null;

      return params.limType === 'classic' ?
      {h: plsmodel_get(model, 'cNh'), q: plsmodel_get(model, 'cNq'), z: plsmodel_get(model, 'cNz')} :
      {h: plsmodel_get(model, 'rNh'), q: plsmodel_get(model, 'rNq'), z: plsmodel_get(model, 'rNz')}
   });

   const u0 = $derived.by(() => {
      if (!model) return null;
      return params.limType === 'classic' ?
      {h: plsmodel_get(model, 'ch0'), q: plsmodel_get(model, 'cq0'), z: plsmodel_get(model, 'cz0')} :
      {h: plsmodel_get(model, 'rh0'), q: plsmodel_get(model, 'rq0'), z: plsmodel_get(model, 'rz0')}
   });

   // get Nu and u0 for selected number of components
   const Na = $derived(N ? {h: N.h[props.ncomp - 1], q: N.q[props.ncomp - 1], z: N.z[props.ncomp - 1]} : null);
   const u0a = $derived(u0 ? {h: u0.h[props.ncomp - 1], q: u0.q[props.ncomp - 1], z: u0.z[props.ncomp - 1]} : null);

   // get Nu and u0 for given axes
   const Nx = $derived(Na ? (params.distType === 'h/q' ? Na.h : Na.h + Na.q) : null);
   const Ny = $derived(Na ? (params.distType === 'h/q' ? Na.q : Na.z) : null);

   const u0x = $derived(u0a ? (params.distType === 'h/q' ? u0a.h : Na.h + Na.q) : null);
   const u0y = $derived(u0a ? (params.distType === 'h/q' ? u0a.q : u0a.z) : null);

   // get conf limits
   const ecl = $derived(params.alpha ? 1 - params.alpha/100 : 0.95);
   const ocl = $derived(params.gamma ? 1 - params.gamma/100 : 0.99);

   // get coordinates for limits
   let els = $derived(Na ? getDistanceLimitCoordinates(ecl, Nx, Ny, params.transType) : null);
   let ols = $derived(Na ? getDistanceLimitCoordinates(Math.pow(ocl, 1/model.nrows), Nx, Ny, params.transType) : null);

   // get plot series
   const series = $derived.by(() => {

      if (!res || !Na || !u0a) return null;

      let xMin = Number.POSITIVE_INFINITY;
      let yMin = Number.POSITIVE_INFINITY;
      let xMax = Number.NEGATIVE_INFINITY;
      let yMax = Number.NEGATIVE_INFINITY;

      const values = [];
      for (let id of plotResIDs) {
         const r = res[id];
         if (!r) return null;
         const h = plsres_get(r, 'hdist', props.ncomp);
         const q = plsres_get(r, 'qdist', props.ncomp);
         const z = plsres_get(r, 'zdist', props.ncomp);

         const x = params.distType === 'h/q' ? h : h.map((h, i) => h / u0a.h * Na.h + q[i] / u0a.q * Na.q);
         const y = params.distType === 'h/q' ? q : z;
         const xv = getDistanceCoords(x, Nx, u0x, params.transType);
         const yv = getDistanceCoords(y, Ny, u0y, params.transType);
         xMin = Math.min(xMin, xv.min);
         yMin = Math.min(yMin, yv.min);
         xMax = Math.max(xMax, xv.max);
         yMax = Math.max(yMax, yv.max);

         values.push({
            xValues: xv.values,
            yValues: yv.values,
            mainColor: LINE_COLORS[id],
            faceColor: FACE_TCOLORS[id],
            marker: MARKERS[id],
            labels: r.objlabels.map(v => v.slice(0, MAX_LABEL_SYMBOLS))
         })
      }

      return {values, xMin, xMax, yMin, yMax};
   })

   // derive plot parameters
   let title = $derived('Distances (A = ' + props.ncomp + ')');
   let xLabel = $derived(getDistanceAxisLabel(params.distType === 'h/q' ? 'h' : 'f', params.transType));
   let yLabel = $derived(getDistanceAxisLabel(params.distType === 'h/q' ? 'q' : 'z', params.transType));

   // ranges of values to define coordinate limits
   let xRange = $state();
   let yRange = $state();


   let limX = $derived.by(() => {
      if (!series) return [0, 1];
      const xMaxTot = Math.max(series.xMax, ols.x.v[ols.x.v.length - 1]);
      return [-0.02 * (xMaxTot - series.xMin), 1.05 * xMaxTot];
   });

   let limY = $derived.by(() => {
      if (!series) return [0, 1];
      const yMaxTot = Math.max(series.yMax, ols.y.v[0]);
      return [-0.02 * (yMaxTot - series.yMin), (1.05 + 0.10 * (params.colorBy !== 'none')) * yMaxTot];
   });

   // compute legend
   let legendItems = $derived(getResLegend(plotResIDs));

</script>

{#if series}
<Axes {title} {limX} {limY} {xLabel} {yLabel} fileName='plot-distance' onclick={callbacks.unselectObject}>

   <!-- limits  -->
   {#if els }
   <Lines xValues={els.x} yValues={els.y} lineColor={LINE_COLOR} lineType={2} />
   {/if}

   {#if ols}
   <Lines xValues={ols.x} yValues={ols.y} lineColor={LINE_COLOR} lineType={3} />
   {/if}

   <!-- data points  -->
   {#each series.values as s}
   <PlotSeries
      {selected}
      {callbacks}
      labels={s.labels}
      mainColor={s.mainColor}
      faceColor={s.faceColor}
      marker={s.marker}
      xValues={s.xValues}
      yValues={s.yValues}
      plotType='p'
      showLabels={params.showLabels}
      bind:xRange={xRange}
      bind:yRange={yRange}
   />
   {/each}

   <Legend items={legendItems} />
</Axes>
{/if}
