<script>
   /*************************************************************/
   /* PlotPLSScores                                             */
   /* --------------------                                      */
   /* shows plot with X or XY scores                            */
   /*************************************************************/

   import { min, max } from 'mdatools/stat';
   import { vector } from 'mdatools/arrays';
   import { Legend, Segments } from 'svelte-plots-basic/2d';
   import { plsres_get } from '../wasm/dist/pls.js';
   import { getResLegend, LINE_COLORS, AXIS_COLOR, MARKERS, FACE_TCOLORS } from './Defaults.js';

   import PlotSeries from './PlotSeries.svelte';
   import Axes from './Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();

   // get the parameters needed for the plot
   let model = $derived(props ? props.model : null);
   let res = $derived(props ? props.res : null);
   let resIDs = $derived(res ? Object.keys(res) : null);
   let plotResIDs = $derived(params && params.resID && resIDs ? (params.resID === 'both' ? resIDs : [params.resID]) : null);
   let resobj = $derived(plotResIDs ? res[plotResIDs[0]] : null);

   // constant plot parameters
   const title = 'Scores';

   function getName(varSet, axis) {
      if (varSet === 'X') return 'X';
      if (varSet === 'Y') return 'Y';
      return axis === 'x' ? 'X' : 'Y';
   }

   function getComp(varSet, comp1, comp2, axis) {
      if (varSet === 'X') return axis === 'x' ? comp1 : comp2;
      if (varSet === 'Y') return axis === 'x' ? comp1 : comp2;
      return comp2;
   }

   function getLabel(params, axis) {
      const prefix = getName(params.varSet, axis);
      const compNum = getComp(params.varSet, params.comp1, params.comp2, axis);
      const variance = model.stat[prefix.toLocaleLowerCase() + 'expvar'][compNum - 1] * 100;
      return prefix + '-scores (LV' +  compNum + ', ' + variance.toFixed(variance > 1 ? 1 : 2) + '%)';
   }

   // compute plot series
   let series = $derived.by(() => {

      if (!plotResIDs || !resobj) return null;

      let minX = Number.POSITIVE_INFINITY;
      let minY = Number.POSITIVE_INFINITY;
      let maxX = Number.NEGATIVE_INFINITY;
      let maxY = Number.NEGATIVE_INFINITY;

      let values = [];
      for (let id of plotResIDs) {
         const r = res[id];

         const x = params.varSet === 'X' | params.varSet === 'XY' ?
            plsres_get(r, 'xscores', (params.varSet === 'XY' ? params.comp2 : params.comp1)) :
            plsres_get(r, 'yscores', params.comp1);

         const y = params.varSet === 'X' ?
            plsres_get(r, 'xscores', params.comp2) :
            plsres_get(r, 'yscores', params.comp2);

         minX = Math.min(minX, min(x));
         minY = Math.min(minY, min(y));
         maxX = Math.max(maxX, max(x));
         maxY = Math.max(maxY, max(y));

         values.push({
            xValues: vector(x),
            yValues: vector(y),
            labels: r.objlabels,
            marker: MARKERS[id],
            faceColor: FACE_TCOLORS[id],
            mainColor: LINE_COLORS[id]
         });
      }

      const dx = (maxX - minX) * 0.05;
      const dy = (maxY - minY) * 0.05;
      const limX = [minX - dx, maxX + dx];
      const limY = [minY - dy, maxY + dy];
      return {limX, limY, values}
   });

   // compute axis titles
   const xLabel = $derived(getLabel(params, 'x'));
   const yLabel = $derived(getLabel(params, 'y'));

   // compute legend
   let legendItems = $derived(getResLegend(plotResIDs));

</script>

{#if series }
<Axes {title}
   {xLabel}
   {yLabel}
   limX={series.limX}
   limY={series.limY}
   fileName={'plot-' + params.varSet.toLowerCase() + 'scores'}
   onclick={callbacks.unselectObject}
   >

   <!-- axis lines -->
   {#if props.ncomp}
   <Segments xStart={[0]} xEnd={[0]} yStart={[series.limY[0]]} yEnd={[series.limY[1]]} lineColor={AXIS_COLOR} />
   <Segments yStart={[0]} yEnd={[0]} xStart={[series.limX[0]]} xEnd={[series.limX[1]]} lineColor={AXIS_COLOR} />
   {/if}


   <!-- data points  -->
   {#each series.values as s, i }
   <PlotSeries
      {selected}
      {callbacks}
      xValues={s.xValues}
      yValues = {s.yValues}
      faceColor={s.faceColor}
      mainColor={s.mainColor}
      marker={s.marker}
      labels = {s.labels}
      showLabels={params.showLabels}
      plotType='p'
      lineWidth={1.5}
   />
   {/each}

   <Legend items={legendItems} />
</Axes>
{/if}