<script>
   /*************************************************************/
   /* AppContainer for creating                                 */
   /* -------------------------                                 */
   /* app container for creating preprocessing chain            */
   /*************************************************************/

   import PrepCreator from '../tab_create/PrepCreator.svelte';
   import AppPlots from './AppPlots.svelte';

   // WASM promise and session object from parent app
   let {
      WASM,
      rawdata,
      outliers,
      left = $bindable(),
      right = $bindable(),
      prepobj = $bindable(),
      meta = $bindable(),
      finalize = null,
   } = $props();

   // get part of the session for this tab
   let varind = $state([-1, 0, 0]);
   let rawdataShort = $state(null);
   let prepdataShort = $state(null);

   // promise for reading the JSON file
   function readFile (e) {
      const reader = new FileReader();
      reader.onload = () => { setModel(reader.result) };
      reader.readAsText(modelFile);
   }


   // callbacks for tracking left and right boundaries
   function setLeft(v) {
      const nvars = rawdata.values.ncols - rawdata.nresp;
      if (rawdata && v >= 0 && v < nvars && v < right) {
         left = v;
      }
   }

   function setRight(v) {
      const nvars = rawdata.values.ncols - rawdata.nresp;
      if (rawdata && v <=nvars && v > left) {
         right = v;
      }
   }

</script>

<div class="widget-container">

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="prep-creator">
         <PrepCreator
            {WASM}
            {rawdata}
            {outliers}
            {left}
            {right}
            bind:varind={varind}
            bind:prepobj={prepobj}
            bind:rawdataShort={rawdataShort}
            bind:prepdataShort={prepdataShort}
            bind:meta={meta}
            {setLeft},
            {setRight}
            {finalize}
         />
      </div>
   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if rawdataShort}
      <AppPlots
         rawdata={rawdataShort}
         prepdata={prepdataShort}
         left={left}
         right={right}
         {varind}
      />
      {/if}
   </div>

</div>


