/**************************************************/
/* Methods for PCR and PCVPLS based on wasm code  */
/**************************************************/

import { ismatrix, Matrix, tcrossprod  } from 'mdatools/arrays';
import { pcares_get } from './pca.js';


/**
 * Returns length of array with PLS results.
 *
 * @param {number} nrows number of rows in a dataset.
 * @param {number} nresp number of response variables.
 * @param {number} ncomp number of components in the model.
 *
 * @returns {number} the length of the results array.
 */
export function pls_getreslength(nrows, nresp, ncomp, npred) {
   // Structure of PLS results:

      // nr * ncomp +         // scores (T)
      // nr * ncomp +         // score distances (H)
      // nr * ncomp +         // orthogonal distances (Q)
      // ncomp +              // cumulative explained variance (xcumexpvar)
      // nr * ny * ncomp +    // predicted values (Yp)
      // nr * ncomp +         // u-scores (U)
      // nr * ncomp +         // residual distances (Z)
      // ncomp +              // cumulative explained variance (ycumexpvar)
      // ny * ncomp +         // RMSE
      // ny * ncomp +         // R2
      // ny * ncomp +         // bias
      // ny * ncomp;          // slope

   return nrows * ncomp +       // T
      nrows * ncomp +           // H
      nrows * ncomp +           // Q
      ncomp +                   // xcumexpvar
      nrows * nresp * ncomp +   // Yp
      nrows * ncomp +           // U
      nrows * ncomp +           // Z
      ncomp +                   // ycumexpvar
      nresp * ncomp +           // RMSE
      nresp * ncomp +           // R2
      nresp * ncomp +           // bias
      nresp * ncomp +           // slope
      nrows * npred;            // x-residuals
}

/**
 * Returns length of array with PLS model.
 *
 * @param {number} npred number of predictors.
 * @param {number} nresp number of responses.
 * @param {number} ncomp number of components
 *
 * @returns {number} the length of the model array.
 */
export function pls_getmodellength(npred, nresp, ncomp) {
   // Structure of PLS model - same as PCA with extra fields at the end:
      // 3 +               // nx, ny and ncomp values
      // 2 * (nx + ny) +   // mean and sd vectors for dataset (x and y together)
      // nx * ncomp +      // x-loadings (P)
      // ncomp +           // x-eigenvalues
      // 4 * ncomp +       // statistics for q-distances
      // 4 * ncomp +       // statistics for h-distances
      // 4 * ncomp +       // statistics for z-distances
      // nx * ncomp +      // weights (R)
      // ny * ncomp +      // y-loadings (C)
      // ncomp;            // y-eigenvalues

   return 3  +                    // nc, ncomp
      2 * (npred + nresp) +       // (mY + mX, sY + sX)
      npred * ncomp +   // P
      1 * ncomp +       // xeigenvals
      4 * ncomp +       // cq0, cNq, rq0, rNq
      4 * ncomp +       // ch0, cNh, rh0, rNh
      4 * ncomp +       // cz0, cNz, rz0, rNz
      npred * ncomp +   // R
      nresp * ncomp +   // C
      1 * ncomp;        // yeigenvals
}


/**
 * Checks if array is correct and all values are within given range.
 *
 * @param {Array} arr - array.
 * @param {number} minVal - smallest expected value.
 * @param {number} maxVal - largest expected value.
 *
 * @returns {boolean} result of check.
 */
function isArrCorrect(arr, minVal, maxVal) {
   if (!Array.isArray(arr)) return false;
   if (arr.length === 0) return true;

   for (let i = 0; i < arr.length; i++) {
      const v = arr[i];
      if (v < minVal || v > maxVal) return false;
   };

   return true;
}

/**
 * Returns part of PLS model array for given outcome.
 *
 * @param {TypedArray} m array with PLS model.
 * @param {string} name name of the outcome (e.g. 'yloadings', 'mY', 'sY', etc.).
 * @param {number} a component number (for loadings).
 *
 * @returns {TypedArray} array with outcomes (by reference).
 */
export function plsmodel_get(m, name, a) {

   if (!m || !m.class || !m.class.includes("plsmodel")) {
      throw new Error('plsmodel_get: parameter "m" must be JSON with PLS model.');
   }

   const npred = m.npred;
   const nresp = m.nresp;
   const ncomp = m.ncomp;

   let length;
   let offset = pls_getmodellength(npred, nresp, ncomp) - npred * ncomp - nresp * ncomp - 2 * nresp;

   switch (name) {

      case 'mY':
         offset = 3;
         length = nresp;
         break;

      case 'mX':
         offset = 3 + nresp;
         length = npred;
         break;

      case 'sY':
         offset = 3 + nresp + npred;
         length = nresp;
         break;

      case 'sX':
         offset = 3 + nresp + npred + nresp;
         length = npred;
         break;

      case 'P':
         offset = 3 + 2 * (nresp + npred );
         length = ncomp * npred;
         break;

      case 'loadings':
      case 'xloadings':
         offset = 3 + 2 * (nresp + npred ) + (a - 1) * npred;
         length = npred;
         break;

      case 'eigenvals':
      case 'xeigenvals':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred;
         length = ncomp;
         break;

      case 'cq0':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + ncomp;
         length = ncomp;
         break;

      case 'cNq':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 2 * ncomp;
         length = ncomp;
         break;

      case 'rq0':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 3 * ncomp;
         length = ncomp;
         break;

      case 'rNq':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 4 * ncomp;
         length = ncomp;
         break;

      case 'ch0':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 5 * ncomp;
         length = ncomp;
         break;

      case 'cNh':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 6 * ncomp;
         length = ncomp;
         break;

      case 'rh0':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 7 * ncomp;
         length = ncomp;
         break;

      case 'rNh':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 8 * ncomp;
         length = ncomp;
         break;

      case 'cz0':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 9 * ncomp;
         length = ncomp;
         break;

      case 'cNz':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 10 * ncomp;
         length = ncomp;
         break;

      case 'rz0':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 11 * ncomp;
         length = ncomp;
         break;

      case 'rNz':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 12 * ncomp;
         length = ncomp;
         break;

      case 'weights':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 13 * ncomp + (a - 1) * npred;
         length = npred;
         break;

      case 'yloadings':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 13 * ncomp + ncomp * npred + (a - 1) * nresp;
         length = nresp;
         break;

      case 'yeigenvals':
         offset = 3 + 2 * (nresp + npred ) + ncomp * npred + 13 * ncomp + ncomp * npred + ncomp * nresp;
         length = ncomp;
         break;
      default:
         throw Error('plsmodel_get: wrong outcome name.');
   }

   return m.v.subarray(offset, offset + length);
}


/**
 * Returns part of PLS result array for given outcome.
 *
 * @param {TypedArray} r array with PLS results.
 * @param {string} name name of the outcome (e.g. 'Yp').
 * @param {number} a component number (for scores, predictions and distances).
 *
 * @returns {TypedArray} array with outcomes (by reference).
 */
export function plsres_get(r, name, a, ny) {

   if (!r || !r.class || !r.class.includes("plsres")) {
      throw new Error('plsres_get: parameter "r" must be JSON with PLS results.');
   }

   if (!ny) {
      ny = 1;
   }


   const nrows = r.nrows;
   const ncomp = r.ncomp;
   const nresp = r.nresp;
   const npred = r.npred;
   const nresp_model = r.nresp_model;

   let length;
   let offset = 3 * nrows * ncomp + ncomp; // T, H, Q + xcumexpvar

   switch (name) {
      case 'Yp':
         offset = offset + (a - 1) * nresp_model * nrows + (ny - 1) * nrows;
         length = nrows;
         break;
      case 'yscores':
         offset = offset + ncomp * nresp_model * nrows + (a - 1) * nrows;
         length = nrows;
         break;
      case 'zdist':
         offset = offset + ncomp * nresp_model * nrows + ncomp * nrows + (a - 1) * nrows;
         length = nrows;
         break;
      case 'ycumexpvar':
         offset = offset + ncomp * nresp_model * nrows + ncomp * nrows + ncomp * nrows;
         length = ncomp;
         break;
      case 'rmse':
         offset = offset + ncomp * nresp_model * nrows + ncomp * nrows + ncomp * nrows + ncomp;
         length = nresp_model * ncomp;
         break;
      case 'r2':
         offset = offset + ncomp * nresp_model * nrows + ncomp * nrows + ncomp * nrows + ncomp + nresp * ncomp;
         length = nresp_model * ncomp;
         break;
      case 'bias':
         offset = offset + ncomp * nresp_model * nrows + ncomp * nrows + ncomp * nrows + ncomp + 2 * nresp * ncomp;
         length = nresp_model * ncomp;
         break;
      case 'slope':
         offset = offset + ncomp * nresp_model * nrows + ncomp * nrows + ncomp * nrows + ncomp + 3 * nresp * ncomp;
         length = nresp_model * ncomp;
         break;
      case 'E':
         offset = offset + ncomp * nresp_model * nrows + ncomp * nrows + ncomp * nrows + ncomp + 4 * nresp * ncomp;
         length = nrows * npred;
         break;
      case 'T':
         offset = 0;
         length = nrows * ncomp;
         break;

      default:
         return pcares_get(r, name, a);
   }

   return r.v.subarray(offset, offset + length);
}




/**
 * Creates a PLS model using wasm implementation.
 *
 * @param {Object} Module object with wasm module for PCV.
 * @param {Matrix} D matrix with training set (predictors and responses).
 * @param {number} nresp number of responses.
 * @param {number} ncomp number of components.
 * @param {boolean} center mean center the data columns or not.
 * @param {boolean} scale standardize the data columns or not.
 * @param {Array} outliers array with indices of outliers.
 * @param {Array} exclvars array with indices of excluded variables.
 *
 * @returns {Object} JSON with PLS model.
 */
export function pls_fit(Module, D, nresp, ncomp, center, scale, outliers, exclvars) {

   if (!ismatrix(D)) {
      throw new Error("pls_fit: D must be a matrix.");
   }

   if (nresp < 1 || D.ncols < (2 + nresp)) {
      throw new Error("pls_fit: training set should have at least 2 predictors and 1 response.");
   }

   // process outliers
   if (!outliers) outliers = [];
   const nout = outliers && Array.isArray(outliers) ? outliers.length : 0;
   if (!isArrCorrect(outliers, 0, D.nrows - 1)) {
      throw new Error('pls_fit: some indices in "outliers" array are outside the expected range.');
   }

   // process excluded variables
   if (!exclvars) exclvars = [];
   const nexv = exclvars && Array.isArray(exclvars) ? exclvars.length : 0;
   if (!isArrCorrect(exclvars, 0, D.ncols - nresp - 1)) {
      throw new Error('pls_fit: some indices in "exclvars" array are outside the expected range.');
   }

   const nrows = D.nrows - nout;
   const npred = D.ncols - nresp - nexv;

   if (nrows < 3 || npred < 2) {
      throw new Error('pls_fit: X must have at least 3 rows and 2 predictors.');
   }

   // set the default values
   if (center === undefined) {
      center = true;
   }

   if (scale === undefined) {
      scale = false;
   }

   if (ncomp === undefined) {
      ncomp = Math.min(nrows - 1, npred);
   }


   if (ncomp < 1 || ncomp > Math.min(nrows - 1, npred)) {
      throw new Error(
         'pls_fit: the value of "ncomp" does not match the dimension of D.',
      );
   }

   // allocate memory in WASM heap space and copy data values
   const d_ptr = Module._malloc(D.v.length * D.v.BYTES_PER_ELEMENT);
   Module.HEAPF64.set(D.v, d_ptr / D.v.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and copy outliers values
   let o_ptr = null;
   if (nout > 0) {
      const o = new Int32Array(outliers);
      o_ptr = Module._malloc(o.length * o.BYTES_PER_ELEMENT);
      Module.HEAP32.set(o, o_ptr / o.BYTES_PER_ELEMENT);
   }

   // allocate memory in WASM heap space and copy excluded variables values
   let ev_ptr = null;
   if (nexv > 0) {
      const ev = new Int32Array(exclvars);
      ev_ptr = Module._malloc(ev.length * ev.BYTES_PER_ELEMENT);
      Module.HEAP32.set(ev, ev_ptr / ev.BYTES_PER_ELEMENT);
   }

   // allocate memory in WASM heap space for model and calibration set results outcomes
   const m_length = pls_getmodellength(npred, nresp, ncomp);
   const m_constructor = Float64Array;
   const m_ptr = Module._malloc(m_length * m_constructor.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space for regression coefficients
   const vip_length = npred * nresp * ncomp;
   const vip_constructor = Float64Array;
   const vip_ptr = Module._malloc(vip_length * m_constructor.BYTES_PER_ELEMENT);
   const coeffs_ptr = Module._malloc(vip_length * m_constructor.BYTES_PER_ELEMENT);
   const sr_ptr = Module._malloc(vip_length * m_constructor.BYTES_PER_ELEMENT);

   // call WASM function for PLS
   const res = Module.ccall(
      'plsfit_wasm',
      [ 'number'], // return type
      [ 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number'],
      [d_ptr, D.nrows, D.ncols - nresp, nresp, ncomp, center, scale, m_ptr, o_ptr, nout, ev_ptr, nexv, coeffs_ptr, vip_ptr, sr_ptr],
   );

   if (res !== 1) {
      throw new Error('pls_fit: unexpected error.');
   }

   // copy part of HEAP memory with model outcomes
   const model = Module.HEAPF64.slice(
      m_ptr / m_constructor.BYTES_PER_ELEMENT,
      m_ptr / m_constructor.BYTES_PER_ELEMENT + m_length,
   );

   // copy part of HEAP memory with coeffs, vip and sr
   const coeffs = Module.HEAPF64.slice(
      coeffs_ptr / vip_constructor.BYTES_PER_ELEMENT,
      coeffs_ptr / vip_constructor.BYTES_PER_ELEMENT + vip_length,
   );

   const vip = Module.HEAPF64.slice(
      vip_ptr / vip_constructor.BYTES_PER_ELEMENT,
      vip_ptr / vip_constructor.BYTES_PER_ELEMENT + vip_length,
   );

   const sr = Module.HEAPF64.slice(
      sr_ptr / vip_constructor.BYTES_PER_ELEMENT,
      sr_ptr / vip_constructor.BYTES_PER_ELEMENT + vip_length,
   );

   // free the memory
   Module._free(d_ptr);
   Module._free(m_ptr);
   Module._free(coeffs_ptr);
   Module._free(vip_ptr);
   Module._free(sr_ptr);
   if (o_ptr) Module._free(o_ptr);
   if (ev_ptr) Module._free(ev_ptr);

   // return PLS model object (can be also treated as PCA model)
   return {
      class: ['plsmodel', 'pcamodel'],
      v: model,
      ncomp: ncomp,
      nrows: nrows,
      npred: npred,
      nresp: nresp,
      exclvars: exclvars,
      center: center,
      scale: scale,
      coeffs: coeffs,
      vip: vip,
      sr: sr
   };
}


/**
 * Projects dataset to a PLS model.
 *
 * @param {Object} Module object with wasm module for PCV.
 * @param {Object} m JSON with PLS model created by `pls_fit()`.
 * @param {Matrix} D matrix with dataset.
 * @param {Number} nresp number of responses in D.
 * @param {Array} outliers array with indices of outliers.
 *
 * @returns {Object} JSON with PLS results.
 */
export function pls_predict(Module, m, D, nresp, outliers) {

   if (!m || !m.class || !m.class.includes('plsmodel')) {
      throw new Error('pls_predict: parameter "m" must be JSON with PLS results.');
   }

   if (!ismatrix(D)) {
      throw new Error('pls_predict: D must be a matrix.');
   }

   // process outliers
   if (!outliers) outliers = [];
   const nout = outliers && Array.isArray(outliers) ? outliers.length : 0;
   if (!isArrCorrect(outliers, 0, D.nrows - 1)) {
      throw new Error('pls_predict: some indices in "outliers" array are outside the expected range.');
   }

   // excluded variables are taken from the model
   let nexv = m.exclvars.length;
   let exclvars = m.exclvars;

   // compute numbers of rows and columns without excluded ones
   const nrows = D.nrows - nout;

   // check if dataset alread excluded variables the ecluded variables array will be emptied
   if ((D.ncols - nresp) === m.npred) {
      exclvars = [];
      nexv = 0;
   }

   // compute new number of predictors
   const npred = D.ncols - nexv - nresp;

   if (nrows < 1) {
      throw new Error('pls_predict: D must have at least 1 row.');
   }

   if (npred !== m.npred) {
      throw new Error('pls_predict: D must have the same number of predictors (without excluded ones) as the training set.');
   }

   // allocate memory in WASM heap space and copy dataset values
   const d_ptr = Module._malloc(D.v.length * D.v.BYTES_PER_ELEMENT);
   Module.HEAPF64.set(D.v, d_ptr / D.v.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space and copy outliers values
   let o_ptr = null;
   if (nout > 0) {
      const o = new Int32Array(outliers);
      o_ptr = Module._malloc(o.length * o.BYTES_PER_ELEMENT);
      Module.HEAP32.set(o, o_ptr / o.BYTES_PER_ELEMENT);
   }

   // allocate memory in WASM heap space and copy excluded variables values
   let ev_ptr = null;
   if (nexv > 0) {
      const ev = new Int32Array(m.exclvars);
      ev_ptr = Module._malloc(ev.length * ev.BYTES_PER_ELEMENT);
      Module.HEAP32.set(ev, ev_ptr / ev.BYTES_PER_ELEMENT);
   }

   // allocate memory in WASM heap space and copy model values
   const m_ptr = Module._malloc(m.v.length * m.v.BYTES_PER_ELEMENT);
   Module.HEAPF64.set(m.v, m_ptr / m.v.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space for result values
   const r_length = pls_getreslength(nrows, m.nresp, m.ncomp, m.npred);
   const r_constructor = Float64Array;
   const r_ptr = Module._malloc(r_length * r_constructor.BYTES_PER_ELEMENT);

   // call WASM function for PCA prediction
   const res = Module.ccall(
      'plspredict_wasm',
      ['number'], // return type
      ['number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number'], // argument types
      [d_ptr, D.nrows, D.ncols - nresp, nresp, m_ptr, r_ptr, o_ptr, nout, ev_ptr, nexv],
   ); // arguments

   if (res !== 1) {
      throw new Error("pls_predict: unexpected error.");
   }

   // copy part of HEAP memory with result values
   const res_values = Module.HEAPF64.slice(
      r_ptr / r_constructor.BYTES_PER_ELEMENT,
      r_ptr / r_constructor.BYTES_PER_ELEMENT + r_length,
   );

   // free the memory
   Module._free(d_ptr);
   Module._free(m_ptr);
   Module._free(r_ptr);
   if (o_ptr) Module._free(o_ptr);
   if (ev_ptr) Module._free(ev_ptr);

   // return PLS results (can be also treated as PCA results)
   const resobj = {
      class: ['plsres', 'pcares'],
      v: res_values,
      ncomp: m.ncomp,
      nrows: nrows,
      npred: npred,
      nresp: nresp,
      nresp_model: m.nresp,
      outliers: outliers,
      exclvars: m.exclvars,
      center: m.center,
      scale: m.scale,
   };

   // compute object with statistics
   const components = new Array(m.ncomp);

   const xexpvar = new Array(m.ncomp);
   const xresvar = new Array(m.ncomp);
   const xcumresvar = new Array(m.ncomp);
   const xcumexpvar = new Array(m.ncomp);

   const yexpvar = new Array(m.ncomp);
   const yresvar = new Array(m.ncomp);
   const ycumresvar = new Array(m.ncomp);
   const ycumexpvar = new Array(m.ncomp);

   const xv = plsres_get(resobj, 'xcumexpvar');
   const yv = plsres_get(resobj, 'ycumexpvar');

   if (nresp > 0) {
      components[0] = 1;

      xcumexpvar[0] = xv[0];
      xcumresvar[0] = 1 - xv[0];
      xexpvar[0] = xv[0];
      xresvar[0] = 1 - xv[0];

      ycumexpvar[0] = yv[0];
      ycumresvar[0] = 1 - yv[0];
      yexpvar[0] = yv[0];
      yresvar[0] = 1 - yv[0];

      for (let i = 1; i < m.ncomp; i++) {
         components[i] = i + 1;

         xcumexpvar[i] = xv[i];
         xcumresvar[i] = 1 - xv[i];
         xexpvar[i] = xv[i] - xv[i - 1];
         xresvar[i] = 1 - xexpvar[i];

         ycumexpvar[i] = yv[i];
         ycumresvar[i] = 1 - yv[i];
         yexpvar[i] = yv[i] - yv[i - 1];
         yresvar[i] = 1 - yexpvar[i];
      }

      const rmse = new Matrix(plsres_get(resobj, "rmse"), m.nresp, m.ncomp);
      const r2 = new Matrix(plsres_get(resobj, "r2"), m.nresp, m.ncomp);
      const slope = new Matrix(plsres_get(resobj, "slope"), m.nresp, m.ncomp);
      const bias = new Matrix(plsres_get(resobj, "bias"), m.nresp, m.ncomp);

      // add statistics and return
      resobj.stat = {
         components,
         xresvar, xexpvar, xcumexpvar, xcumresvar,
         yresvar, yexpvar, ycumexpvar, ycumresvar,

         rmse: rmse.t(),
         r2: r2.t(),
         slope: slope.t(),
         bias: bias.t(),
      };
   } else {
      resobj.stat = null;
   }

   return resobj;
}


/**
 * Create a PV-set based on PLS decomposition.
 *
 * @param {Object} Module object with wasm module for PCV.
 * @param {Object} m JSON with PLS model created by `pcr_fit()`.
 * @param {Matrix} D matrix with training set.
 * @param {number} nresp number of responses in D.
 * @param {number} ncomp number of components for PV-set generation.
 * @param {number} cv_nseg number of segments for cross-validation.
 * @param {number} cv_type type of cross-validation (0: loo, 1: ven, 2: rand).
 * @param {number} cv_scope scope of cross-validation (0: global, 1: local).
 * @param {Array} outliers array with indices of outliers.
 *
 * @returns {Array} array with two matrices: the generated PV-set and matrix of scalars D.
 */
export function pcvpls(Module, m, D, nresp, ncomp, cv_nseg, cv_type, cv_scope, outliers) {

   if (!m || !m.class || !m.class.includes('plsmodel')) {
      throw new Error('pcvpls: parameter "m" must be JSON with PLS model.');
   }

   if (!ismatrix(D)) {
      throw new Error('pcvpls: D must be a matrix.');
   }

   if (nresp < 1) {
      throw new Error('pcvpls: D must have at least one response.');
   }

   // process outliers
   if (!outliers) outliers = [];
   const nout = outliers && Array.isArray(outliers) ? outliers.length : 0;
   if (!isArrCorrect(outliers, 0, D.nrows - 1)) {
      throw new Error('pca_predict: some indices in "outliers" array are outside the expected range.');
   }

   // excluded variables are taken from the model
   const nexv = m.exclvars.length;

   // compute numbers of rows and columns without excluded ones
   const nrows = D.nrows - nout;
   const npred = D.ncols - nresp - nexv;


   if (nrows < 3) {
      throw new Error('plspcv: D must have at least 3 rows.');
   }

   if (npred < 2) {
      throw new Error('plspcv: D must have at least 2 predictors.');
   }


   if (!Number.isInteger(cv_type) || ![0, 1, 2].includes(cv_type)) {
      throw new Error('plspcv: parameter "cv_type" must be one of 0 ("loo"), 1 ("ven"), 2 ("rand).');
   }

   if (!Number.isInteger(cv_scope) || ![0, 1].includes(cv_scope)) {
      throw new Error('plspcv: parameter "cv_scope" must be 0 ("global") or 1 ("local").');
   }

   if (cv_type > 0 && (!Number.isInteger(cv_nseg) || cv_nseg < 2)) {
      throw new Error('plspcv: parameter "cv_nseg" must be a number  2.');
   }

   if (cv_type > 0 && cv_nseg > nrows) {
      throw new Error('plspcv: the value of "cv_nseg" is too large for the given X.');
   }

   if (cv_type === 0) {
      cv_nseg = nrows;
   }

   if (!Number.isInteger(ncomp) || ncomp < 1) {
      throw new Error('plspcv: the value of "ncomp" must be a number >= 1.');
   }

   const max_ncomp = cv_type === 0 ?
      Math.min(nrows - 2, npred, m.ncomp) :
      Math.min(nrows - Math.ceil(nrows / cv_nseg) - 1, npred, m.ncomp);

   if (ncomp > max_ncomp) {
      throw new Error('pcapcv: the value of "ncomp" is too large.');
   }

   // allocate memory in WASM heap space and copy dataset values
   const d_ptr = Module._malloc(D.v.length * D.v.BYTES_PER_ELEMENT);
   Module.HEAPF64.set(D.v, d_ptr / D.v.BYTES_PER_ELEMENT);


   // allocate memory in WASM heap space and copy outliers values
   let o_ptr = null;
   if (nout > 0) {
      const o = new Int32Array(outliers);
      o_ptr = Module._malloc(o.length * o.BYTES_PER_ELEMENT);
      Module.HEAP32.set(o, o_ptr / o.BYTES_PER_ELEMENT);
   }

   // allocate memory in WASM heap space and copy excluded variables values
   let ev_ptr = null;
   if (nexv > 0) {
      const ev = new Int32Array(m.exclvars);
      ev_ptr = Module._malloc(ev.length * ev.BYTES_PER_ELEMENT);
      Module.HEAP32.set(ev, ev_ptr / ev.BYTES_PER_ELEMENT);
   }


   // allocate memory in WASM heap space and copy model values
   const m_ptr = Module._malloc(m.v.length * m.v.BYTES_PER_ELEMENT);
   Module.HEAPF64.set(m.v, m_ptr / m.v.BYTES_PER_ELEMENT);

   // allocate memory in WASM heap space for Xpv values
   const dpv_constructor = D.v.constructor;
   const dpv_length = nrows * (nresp + npred);
   const dpv_ptr = Module._malloc(dpv_length * dpv_constructor.BYTES_PER_ELEMENT,);

   // allocate memory in WASM heap space for scalar values
   const s_length = cv_nseg * ncomp;
   const s_constructor = D.v.constructor;
   const s_ptr = Module._malloc(s_length * s_constructor.BYTES_PER_ELEMENT);

   // call WASM function for PCAPCV
   const res = Module.ccall('pcvpls_wasm',
               ['number'],
               ['number', 'number', 'number', 'number', 'number', 'number', 'number',
                'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number', 'number'],
               [d_ptr, D.nrows, D.ncols - nresp, nresp, ncomp, m.center, m.scale,
               cv_type, cv_nseg, cv_scope, m_ptr, dpv_ptr, s_ptr, o_ptr, nout, ev_ptr, nexv]);


   if (res !== 1) {
      throw new Error('plspcv: unexpected error.');
   }

   // copy part of HEAP memory with Xpv values
   const dpv = Module.HEAPF64.slice(
      dpv_ptr / dpv_constructor.BYTES_PER_ELEMENT,
      dpv_ptr / dpv_constructor.BYTES_PER_ELEMENT + dpv_length,
   );

   // copy part of HEAP memory with D values
   const s = Module.HEAPF64.slice(
      s_ptr / s_constructor.BYTES_PER_ELEMENT,
      s_ptr / s_constructor.BYTES_PER_ELEMENT + s_length,
   );

   // free the memory
   Module._free(d_ptr);
   Module._free(m_ptr);
   Module._free(dpv_ptr);
   Module._free(s_ptr);
   if (o_ptr) Module._free(o_ptr);
   if (ev_ptr) Module._free(ev_ptr);

   // return Xpv
   return [new Matrix(dpv, nrows, (nresp + npred)), new Matrix(s, cv_nseg, ncomp)];
}
