<script>
   /****************************************************************/
   /* PlotRegcoeffs                                                */
   /* --------------------                                         */
   /* shows plot with PLS loadings                                 */
   /****************************************************************/

   import { Segments } from 'svelte-plots-basic/2d';
   import { vector, Matrix } from 'mdatools/arrays';
   import { mrange } from 'mdatools/stat';
   import { AXIS_COLOR, LOADINGS_LINE_COLOR  } from './Defaults.js';

   import PlotSeries from './PlotSeries.svelte';
   import Axes from './Axes.svelte';


   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();

   // get model object from properties
   let model = $derived(props ? props.model : null);

   // get array with coefficients and convert it to matrix
   let coeffsVec = $derived(model && props.ncomp > 0 ? model.coeffs.subarray(
      (props.ncomp - 1) * model.npred * model.nresp,
      (props.ncomp - 1) * model.npred * model.nresp + model.npred * model.nresp)
   : null);

   let coeffs = $derived(coeffsVec ? new Matrix(coeffsVec, model.npred, model.nresp) : null);

   // get values and indices for x-variables
   const axisValues = $derived(model ? vector(model.xvarvalues) : null);
   const axisIndices = $derived(model ? vector(model.xvarindices) : null);
   const xlabels = $derived(model ? model.xvarlabels : null);
   const xAxisLabel = $derived(model ? model.varvaluesName + (model.varvaluesUnits ? ', ' + model.varvaluesUnits : '') : '');

   const xValues = $derived(params.plotType === 'h' ? axisIndices : axisValues);
   const yValues = $derived(coeffs ? coeffs.getcolumn(params.resp) : null);
   const xLabel = $derived(params.plotType === 'h' ? 'Predictors' : xAxisLabel);


   const limX = $derived(params.plotType === 'h' && xValues.length < 20 ? [0.5, xValues.length + 0.5] : (xValues ? (mrange(xValues, 0.05)) : [0, 1]));
   const limY = $derived(yValues ? mrange(yValues, 0.075) : [0, 1]);
   const title = $derived('Regression coefficients (A = ' + props.ncomp + ')');
   const yLabel = 'Coefficients';
</script>

{#if xValues && yValues }
<Axes {xLabel} {yLabel} {limX} {limY} {title} whole={params.plotType==='h'} fileName='plot-regcoeffs'>

   <!-- axis lines (x = 0 and y = 0) -->
   <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[0]} yEnd={[0]} lineColor={AXIS_COLOR} />

   <!-- data points  -->
   <PlotSeries
      {xValues}
      {yValues}
      {selected}
      plotType={params.plotType}
      showLabels={params.showLabels}
      mainColor={LOADINGS_LINE_COLOR}
      labels={xlabels}
      shortenLabels={false}
   />


</Axes>
{/if}
