<script>
   /*************************************************************/
   /* PlotPLSVariance                                           */
   /* --------------------                                      */
   /* shows plot with variance values vs. number of PCs         */
   /*************************************************************/

   import { vector } from 'mdatools/arrays';
   import { min } from 'mdatools/stat';
   import { Legend, Segments } from 'svelte-plots-basic/2d';
   import { LINE_COLORS, FACE_COLORS, MARKERS, getResLegend } from './Defaults.js';
   import PlotSeries from './PlotSeries.svelte';
   import Axes from './Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();

   // get the parameters needed for the plot
   let res = $derived(props ? props.res : null);
   let resIDs = $derived(res ? Object.keys(res) : null);
   let plotResIDs = $derived(params && params.resID && resIDs ? (params.resID === 'both' ? resIDs : [params.resID]) : null);
   let resobj = $derived(plotResIDs ? res[plotResIDs[0]] : null);

   // constant plot parameters
   const titles = {
      'expvar': 'Explained variance',
      'resvar': 'Residual variance',
      'cumexpvar': 'Cumulative explained variance',
      'cumresvar': 'Cumulative residual variance'
   };

   const varSet = $derived(params.varSet === 'X' ? 'x' : 'y');
   const varForm = $derived(params.varForm === 'cumulative' ? 'cum' : '');
   const varType = $derived(params.varType === 'explained' ? 'exp' : 'res');
   const statName = $derived(varForm + varType + 'var');
   const statNameFull = $derived(varSet + varForm + varType + 'var');
   const title = $derived(statName ? titles[statName] + ' (' + params.varSet + ')' : '');

   const xMin = $derived(resobj.ncomp > 1 ? 1 - resobj.ncomp * 0.065 : -0.1);
   const xMax = $derived(resobj.ncomp > 1 ? resobj.ncomp * 1.065 : 2.1);
   const limX = $derived([xMin, xMax]);

   const xValues = $derived(resobj ? resobj.stat.components : null);

   const series = $derived(plotResIDs ? plotResIDs.map(v => {
      const y = res[v].stat[statNameFull];
      return {
         yValues: vector(y),
         labels: Array.from(y).map(yy => yy.toFixed(yy < 0.1 ? 3 : 2)),
         faceColor: FACE_COLORS[v],
         mainColor: LINE_COLORS[v],
         marker: MARKERS[v]
      }
   }) : null);

   // compute y-limit
   const yMin = $derived(series ? min(series.map(v => Math.min(-0.05, min(v.yValues)))): 0);
   const limY = $derived([yMin * 1.10, 1.1]);

   // compute legend
   const legendItems = $derived(getResLegend(plotResIDs));
</script>

{#if xValues && series }
<Axes {title} {limX} {limY}
   xLabel={'Number of LVs'}
   yLabel={'Variance'}
   fileName={'plot-' + statNameFull}
   whole={true}>

   <!-- vertical line with selected component -->
   {#if props.ncomp}
   <Segments xStart={[props.ncomp]} xEnd={[props.ncomp]} yStart={[limY[0]]} yEnd={[limY[1]]} lineColor='#a0a0a0' />
   {/if}


   <!-- data points  -->
   {#each series as s, i }
   <PlotSeries
      {xValues}
      yValues = {s.yValues}
      labels = {s.labels}
      faceColor={s.faceColor}
      mainColor={s.mainColor}
      marker={s.marker}
      plotType='b'
      showLabels={params.showLabels}
      lineWidth={1.5}
   />
   {/each}

   <!-- data points  for selected component -->
   {#if props.ncomp}
   {#each series as s, i }
   <PlotSeries
      xValues={[xValues[props.ncomp - 1]]}
      yValues={[s.yValues.v[props.ncomp - 1]]}
      faceColor={s.faceColor}
      mainColor={s.mainColor}
      plotType='b'
      showLabels={params.showLabels}
      marker={s.marker}
      markerWidth={3.0}
   />
   {/each}
   {/if}

   <Legend items={legendItems} />
</Axes>
{/if}