<script>
   /****************************************************************/
   /* PlotPVScalars                                                */
   /* --------------------                                         */
   /* shows heatmap with PCVPLS scalars                            */
   /****************************************************************/

   import { Heatmap, ColormapLegend, Axes, XAxis, YAxis, Box } from 'svelte-plots-basic/2d';



   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();

   // get model object from properties
   let scalars = $derived(props ? props.scalars : null);

   const breaks = [-99999, -2, -1, 0, 0.2, 0.5, 0.8, 1.2, 1.5, 1.8, 2.0, 2.5, 3.0, 99999];
   const colmap = [
      '#6200DF', // < -2
      '#6923FF', // -2.00 to -1.00
      '#648FFF', // -1.00 to  0.00
      '#8CCDFD', //  0.00 to  0.25
      '#D2ECFF', //  0.25 to  0.50
      '#E8F5FF', //  0.50 to  0.75
      '#fefeff', //  0.75 to  1.25
      '#FFF5E5', //  1.25 to  1.50
      '#FFE5BE', //  1.50 to  1.75
      '#FFCE85', //  1.75 to  2.00
      '#FFA34E', //  2.00 to  2.50
      '#FF8024', //  2.50 to  3.00
      '#CF5903'  //  > 3.0
   ];

   const labels = breaks.map(v => v.toFixed(1));
   labels[0] = '&minus;&infin;';
   labels[labels.length - 1] = '&infin;';

   const dY = $derived(scalars ? scalars.nrows / 8 : 1);
   const limX = $derived(scalars ? [0.45, scalars.ncols + 0.6] : [0, 1]);
   const limY = $derived(scalars ? [0.45, scalars.nrows + Math.max(0.9, dY)] : [0, 1]);

   const title = 'PV-scalars';
   const yLabel = 'Segments, k';
   const xLabel = 'Components, A';
</script>

{#if scalars }
<Axes {limX} {limY} {title} fileName='plot-pvscalars'>
   <Heatmap values={scalars} {breaks} {colmap}/>
   <ColormapLegend {breaks} {colmap} {labels}/>
   <XAxis label={xLabel} whole={true} />
   <YAxis label={yLabel} whole={true} />
   <Box />
</Axes>
{/if}
