<script>
   /**************************************************************/
   /* DataLoader for PLS app                                     */
   /* -------------------------                                  */
   /* Loads test set from CSV file and returns is main output.   */
   /* If PLS model is provided it also applied the model and     */
   /* returns object with PLS results. In this case it will      */
   /* also recalculate the result object if array with outliers  */
   /* has been changed                                           */
   /**************************************************************/


   import { FileSelector, Switch, Spinner, Select, Container, Button, TextInput } from 'svelte-controls-basic';
   import { parseCSV, app_pls_predict, parseCSVParams, readSPCFiles } from '../../../shared/utils.js';
   import DatasetWidget from '../../../shared/widgets/DatasetWidget.svelte';


   let {
      WASM,                             // WASM object to make predictions if model is available (optional)
      model = null,                     // object with PLS model (optional)
      outliers = [],                    // array with outlier indices (optional)
      respMandatory = false,            // logical, if response columns is mandatory in the data file
      title = '',                       // title, to show on top of the block
      dataset = $bindable(),            // JSON with datasets
      resobj = $bindable(),             // JSON with PLS results (if model is provided)
   } = $props();

   // possible delimeters and header options
   const sepOptions = [',', ';'];
   const headerOptions = $derived(model ? ['no', 'yes'] : ['no', 'labels', 'values']);
   const classMandatory = false;

   // data properties
   let dataFile = $state();                     // CSV file
   let varvaluesName = $state('');              // name of the variable values to be used as header
   let varvaluesUnits = $state('');             // units of the variable values
   let hasClassLabels = false;                  // we do not expect classes but keep it for compatibility

   // data properties, that will be autodetected from CSV file
   let sep = $state(',');                       // seperator between values
   let header = $state('no');                   // first row contains header (labels or values)
   let hasLabels = $state(false);               // first column contains row labels
   let nCols = $state(0);                       // number of columns in dataset
   let nRespMax = $derived(1);                  // maximum number of responses (for now is 1) !!! for future use !!!
   let nResp = $derived(respMandatory ? 1 : 0); // number of responses !!! for now it is fixed with one response (or 0 for predictions)


   // promise, app status for handling and showing errors
   let promise = $state(null);
   let status = $state('neutral');
   let errMsg = $state('');

   // method for showing error
   function setError(msg) {
      status = 'error';
      errMsg = msg;
      console.error(msg)
   }

   // method for setting neutral state
   function setNeutral() {
      status = 'neural';
      errMsg = '';
   }

   // method for setting success state
   function setSuccess() {
      status = 'success';
      errMsg = '';
   }


   // method to apply PLS model
   function predict(model, dataset, outliers) {

      // if result object is already defined do nothing
      if (resobj) return;
      promise = WASM()
         .then(Module => {
            resobj = app_pls_predict(Module, model, dataset, outliers, true);
            setSuccess();
         })
         .catch(e =>{
            resobj = null;
            setError(e);
         });
   };

   // method for guessing CSV parameters
   function guessParams(dataFile, model) {

      // no data file is provided - no guessing
      if (!dataFile || dataFile.size < 10) return;

      // read first 100Kb
      const chunkSize = 100000;
      const first100KB = dataFile.slice(0, chunkSize);

      const reader = new FileReader();
      reader.onload = () => {
         const csvString = reader.result;
         if (!csvString || csvString.length < 0) return;
         [sep, hasLabels, hasClassLabels, header, nCols] = parseCSVParams(csvString, nResp);
         if (model && header !== 'no') header = 'yes';
         setSuccess();
      };

      reader.onerror = function(e) {
         csvString = null;
         setError('Error reading CSV file. ' + e);
      };

      reader.readAsText(first100KB);
   }

   // method for parsing CSV content provided as one large string to dataset object.
   function setData(strData) {
      try{

         // right now we keep nResp = 1 if responses are required otherwise 0
         const d = parseCSV(
            strData,
            sep,
            header === 'yes' ? 'labels' : header, // when load data for prediction simpler interface is used
            hasLabels,
            hasClassLabels,
            varvaluesName,
            varvaluesUnits,
            nResp
         );

         // check that the number of rows is sufficient
         if (d.values.nrows < 1 ) {
            throw new Error('Dataset must have at least 1 row.');
         }

         // check that number of variables is sufficient
         if ((d.values.ncols - d.nresp) < 2) {
            throw new Error('Dataset should habe at least two predictors.');
         }


         // if model is available inherit variable properties from the model
         if (model) {

            // check that number of variables match
            const npredExpected = (model.prep && model.prep.npred) ? model.prep.npred : model.npred + model.exclvars.length;
            const ncolsExpected = respMandatory ? npredExpected + model.nresp : npredExpected;

            if (d.values.ncols !== ncolsExpected) {
               throw new Error('Dataset has different number of columns than the training set.');
            }

            // if data values in the training set were in reverse order it is expected
            // that they are in the reverse order in this dataset as well
            d.varrev = model.varrev;
            d.header = model.header;
            d.varlabels = model.varlabels;
            d.varvalues = model.varvalues;
            d.varindices = model.varindices;
            d.varvaluesName = model.varvaluesName;
            d.varvaluesUnits = model.varvaluesUnits;
         }

         d['fileName'] = dataFile.name.replace(/\.[^/.]+$/, "");

         dataset = d;
         setSuccess();
      } catch (e) {
         dataset = null;
         setError(e);
      }
   }

   // method for reading the CSV file
   function readCSV (e) {
      const reader = new FileReader();
      reader.onload = () => { setData(reader.result) };
      reader.readAsText(dataFile);
   }

   // metgod for reading the SPC file
   async function readSPC (dataFiles) {
      try {
         const dataFiles = dataFile instanceof FileList ? dataFile : [dataFile];
         dataset = await readSPCFiles(dataFiles);
         setSuccess();
      } catch (e) {
         setError('Failed to read SPC file: ' + e);
      }
   }

   // reset whole component to initial state of the component
   function reset () {
      dataFile = '';
      dataset = null;
      resobj = null;
      varvaluesName = '';
      varvaluesUnits = '';
      setNeutral();
   }

   // if data file is selected - guess parameters
   $effect(() => {

      if (dataFile === 0 || dataFile === undefined || dataset) return;

      // if data file is empty reset status and error messages
      if (dataFile === null || dataFile === '') {
         reset();
         return;
      }

      // dataFile is array with several SPC files
      if (Array.isArray(dataFile)) {

         if (dataFile.filter(v => v.slice(-4) === '.spc').length !== dataFile.length) {
            setError('Multiple file selection is allowed only for SPC files.');
            return;
         }

         readSPC(dataFile);
         return;
      }

      if (dataFile && (dataFile.type === 'text/csv' || dataFile.type === 'application/vnd.ms-excel')) {
         // if not just remove error messages if any and guess the parameters
         guessParams(dataFile, model);
         setNeutral();
      } else {
         readSPC([dataFile]);
      }
   });

   // if separator was changed remove error message
   $effect(() => {
      if (sep) {
         setNeutral();
      }
   });

   // if dataset, model or array with outliers have been changed, make new predictions
   $effect(() => {
      if (dataset && model && !resobj && !errMsg) {
         try{
            predict(model, dataset, outliers);
            setSuccess();
         } catch (e) {
            dataset = null;
            resobj = null;
            setError(e);
         }
      }
   });
</script>

{#if !dataset }

   <!-- show if data is not loaded -->
   <!-- reset component if dataFile is changed inside -->
   {#key dataFile === undefined}
   <Container>
      {#if respMandatory}
      <FileSelector
         id="dataFile"
         message="Select CSV file with dataset"
         bind:file={dataFile}
      />
      {:else}
      <FileSelector
         id="dataFile"
         message="Select CSV or SPC file with dataset"
         acceptType={'.csv, .spc'}
         multiple={true}
         bind:file={dataFile}
      />
      {/if}
   </Container>
   {/key}

   <!-- show if file is selected but data is not loaded -->
   {#if dataFile && !Array.isArray(dataFile) && (dataFile.type === 'text/csv' || dataFile.type === 'application/vnd.ms-excel')}
   <Container label="Delimiter">
      <Select id="sep" bind:value={sep} options={sepOptions} />
   </Container>
   <Container label="Row labels">
      <Switch id="hasLabels" bind:value={hasLabels} options={['no', 'yes']} />
   </Container>
   <!-- !!! for now let nresp always be 1
   {#if !respMandatory}
   <Container label="# responses">
      <Range id="nresp" bind:value={nResp} min={1} max={nRespMax} step={1} decNum={0}/>
   </Container>
   {/if}
   -->
   <Container label="Header">
      <Select id="header" bind:value={header} options={headerOptions} />
   </Container>
   {#if header==='values'}
   <Container label="Header name (predictors)">
      <TextInput id="varvaluesName" bind:value={varvaluesName} placeholder="e.g. Wavelength" />
   </Container>
   <Container label="Header units (predictors)">
      <TextInput id="varvaluesUnits" bind:value={varvaluesUnits} maxLength={10} placeholder="e.g. nm" />
   </Container>
   {/if}
   <Container label="&nbsp;">
      <Button text='Load' onclick={readCSV}/>
   </Container>
   {/if}

{:else}


   {#if promise}

      <!-- calculation is in process -->
      {#await promise}
         <Container>
            <Spinner />
            <p>{model ? 'Loading data and apply model...' : 'Loading data...'}</p>
         </Container>

      {:catch error}
         <!-- error message if failed -->
         <Container status="error">
            <p>{error}</p>
         </Container>
      {/await}

   {/if}

{/if}

{#if dataset }
   <!-- dataset is loaded - show info and cancel button -->
   <DatasetWidget {dataset} {reset} {title} />
{/if}

<!-- error message -->
{#if status === 'error' || errMsg}
<Container status="error">
   <p>{errMsg}</p>
</Container>
{/if}

