<script>
   /*************************************************************/
   /* PlotPredictions                                           */
   /* --------------------                                      */
   /* shows plot with predicted vs measured y-values            */
   /*************************************************************/

   import { vector } from 'mdatools/arrays';
   import { Legend, Lines, TextLabels } from 'svelte-plots-basic/2d';
   import { min, max, cov, variance, mean } from 'mdatools/stat';

   import { LINE_COLORS, FACE_COLORS, FACE_TCOLORS, MARKERS, val2labels, getResLegend } from './Defaults.js';
   import {plsres_get} from '../wasm/dist/pls.js';

   import PlotSeries from './PlotSeries.svelte';
   import Axes from './Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();


   // get the parameters needed for the plot
   const res = $derived(props ? props.res : null);
   const resIDs = $derived(res ? Object.keys(res) : null);
   const plotResIDs = $derived(params && params.resID && resIDs ? (params.resID === 'both' ? resIDs : [params.resID]) : null);
   const resobj = $derived(plotResIDs && res[plotResIDs[0]] ? res[plotResIDs[0]] : null);

   // check if reference values are available
   const hasRef = $derived(resobj && resobj.Yref ? true : false);

   // plot parameters
   const title = $derived(resobj ? 'Predictions for ' + resobj.varlabels[params.resp - 1] + ' (A = ' + props.ncomp + ')' : '');
   const xLabel = $derived(hasRef ? 'Reference y-values' : 'Objects');

   // statistics
   const stats = $derived.by(() => {
      if (!resobj || !resobj.stat || !params.showStat || params.resID === 'both') return null;
      const id = params.resID;
      const rmse = res[id].stat.rmse.getcolref(params.resp)[props.ncomp - 1];
      const r2 = res[id].stat.r2.getcolref(params.resp)[props.ncomp - 1];
      const slope = res[id].stat.slope.getcolref(params.resp)[props.ncomp - 1];
      return   '<tspan x="5em" dy="1.5em">RMSE: ' + rmse.toFixed(2) + '</tspan>' +
               '<tspan x="5em" dy="1.5em">R2: ' + r2.toFixed(3) + '</tspan>' +
               '<tspan x="5em" dy="1.5em">Slope: ' + slope.toFixed(3) + '</tspan></tspan>';

   });

   // plot series
   const series = $derived.by(() => {
      if (!plotResIDs || !resobj) return null;

      let vMin = params.resp ? Number.POSITIVE_INFINITY : null;
      let vMax = params.resp ? Number.NEGATIVE_INFINITY : null;
      let vNum = 0;
      let values = [];
      let stats = '';

      for (let id of plotResIDs) {
         const r = res[id];
         const yr = r.Yref ? r.Yref.getcolref(params.resp) : r.objindices.map(v => v + 1);
         const yp = plsres_get(r, 'Yp', props.ncomp, params.resp);
         const b1 = cov(yr, yp) / variance(yr);
         const b0 = mean(yp) - b1 * mean(yr);
         vMin = Math.min(vMin, min(yr), min(yp));
         vMax = Math.max(vMax, max(yr), max(yp));
         vNum += yr.length;

         values.push({
            xValues: vector(yr),
            yValues: vector(yp),
            lineX: [vMin, vMax],
            lineY: [b0 + b1 * vMin, b0 + b1 * vMax],
            labels: params.showLabels === 'labels' ? r.objlabels : val2labels(yp, vMin, vMax),
            faceColor: FACE_TCOLORS[id],
            mainColor: LINE_COLORS[id],
            marker: MARKERS[id],
            stats: stats
          })
      }

      return { vMin, vMax, vNum, values };
   });

   // legend
   let legendItems = $derived(getResLegend(plotResIDs));

   // y-axis limits
   const dl = $derived(series ? (series.vMax - series.vMin) * 0.075 : null)
   const lim = $derived(dl ? [series.vMin - dl, series.vMax + dl] : [0, 1]);

</script>

{#if series }
<Axes {title} {xLabel}
   limX={hasRef ? lim : [0, series.vNum + 1]}
   limY={lim}
   yLabel='Predicted y-values'
   fileName='plot-predictions'
   onclick={callbacks.unselectObject}
   >
   <!-- ideal line -->
   {#if hasRef}
   <Lines xValues={lim} yValues={lim} lineColor='#d0d0d0' />
   {/if}

   <!-- data points  -->
   {#each series.values as s, i }
   {#if hasRef}
   <Lines
      xValues={s.lineX}
      yValues={s.lineY}
      lineColor={s.mainColor}
   />
   {/if}
   <PlotSeries
      {selected}
      {callbacks}
      xValues={s.xValues}
      yValues={s.yValues}
      faceColor={s.faceColor}
      mainColor={s.mainColor}
      labels = {s.labels}
      marker = {s.marker}
      showLabels={params.showLabels !== 'hide'}
      plotType={hasRef ? 'p' : 'h'}
   />
   {/each}

   {#if stats}
   <TextLabels textSize={0.85} pos={0} xValues={[lim[0]]} yValues={[lim[1] - dl]} labels={stats} />
   {/if}

   <Legend items={legendItems} position="bottomright"/>
</Axes>
{/if}