import {Select, PlotTypeSelector, RangeDiscrete, Switch} from 'svelte-controls-basic';

export function getPlotAcceptanceControls(res) {

   const resIDs = Object.keys(res);
   const resobj = res[resIDs[0]];
   const hasBoth = resobj.nMembers > 0 && resobj.nStrangers > 0;

   const subsetDefault = resobj.nMembers > 0 ? 'members' : (resobj.nStrangers > 0 ? 'strangers' : 'all')

   const controls = {
      resID: { el: Select, hidden: resIDs.length == 1, default: resIDs[0], label: 'Show', props: {options: resIDs}},
      subset: { el: Select, hidden: !hasBoth, default: subsetDefault, label: 'Show', props: {options: ['members', 'strangers', 'all']}},
      transType: { el: Select, hidden: false, default: 'no', label: 'Transform', props: {options: ['no', 'log']}},
      showLabels: { el: Switch, hidden: false, default: false, label: 'Labels', props: {options: ['hide', 'show']}},
   };

   if (resobj.nStrangers > 0 && resobj.nStrangers == 0 && resobj.classNames && resobj.classNames.length > 1) {
      controls['colorBy'] = { el: Select, hidden: false, default: 'roles', label: 'Color by', props: {options: ['roles', 'classes']}}
   }

   return controls;
}

export function getPlotClassStatControls(res) {

   const resIDs = Object.keys(res);
   const resobj = res[resIDs[0]];

   const resOptions = [...resIDs, 'both'];
   const fomOptions = resobj.nMembers > 0 ?
      (resobj.nStrangers > 0 ? ['sens', 'spec', 'eff', 'sel'] : ['sens']) : ['spec', 'sel'];

   const controls = {
      resID: { el: Select, hidden: resIDs.length == 1, default: resIDs[0], label: 'Show', props: {options: resOptions}},
      statName: { el: Select, hidden: fomOptions.length == 1, default: fomOptions[0], label: 'FoM', props: {options: fomOptions}},
      zoom: { el: Switch, hidden: false, default: false, label: 'Zoom', props: {options: ['off', 'on']}},
   };

   return controls;
}

