export function getPlotScoresControls(res) {

   const resIDs = Object.keys(res);
   const resobj = res[resIDs[0]];
   const colorByOptions = resobj.classNames && resobj.classNames.length > 1 ? ['none', 'groups', 'variable'] : ['none', 'variable'];
   const resOptions = resIDs.concat(['both']);

   const controls = {
      resID: { el: Select, hidden: resIDs.length == 1, default: resIDs[0], label: 'Show', props: {options: resOptions}},
      plotType: { el: PlotTypeSelector, hidden: false, default: 'p', label: 'Plot type', props: {options: ['p', 'l', 'h']}},
      comp2: { el: RangeDiscrete, hidden: false, default: resobj.ncomp > 1 ? 2 : 1, label: 'PC (y-axis)', props: {min: 1, max: resobj.ncomp}},
      comp1: { el: RangeDiscrete, hidden: false, default: 1, label: 'PC (x-axis)', props: {min: 1, max: resobj.ncomp}},
      showLabels: { el: Switch, hidden: false, default: false, label: 'Labels', props: {options: ['hide', 'show']}},
      showEllipse: { el: Switch, hidden: false, default: false, label: 'T2 ellipse', props: {options: ['hide', 'show']}},
      colorBy: { el: Select, hidden: false, default: 'none', label: 'Color by', props: {options: colorByOptions}},
      colorByVar: { el: RangeDiscrete, hidden: true, default: 1, label: 'Variable', props: {min: 1, max: resobj.ncols}},
      confEllipse: { el: Select, hidden: true, default: 'none', label: 'Conf ellipse', props: {options: ['none', '90', '95', '99']}},
   };

   return controls;
}

