export function getPlotPLSVarianceControls(res) {

   const resIDs = Object.keys(res);
   const options = resIDs.length > 1 ? [...resIDs, 'both'] : resIDs;
   const defaultOption = resIDs.length > 1 ? 'both' : resIDs[0];

   const controls = {
      resID: { el: Select, hidden: resIDs.length == 1, default: defaultOption, label: 'Show', props: {options: options}},
      varSet: { el: Select, hidden: false, default: 'X', label: 'Variance for', props: {options: ['X', 'Y']}},
      varType: { el: Select, hidden: false, default: 'explained', label: 'Type', props: {options: ['explained', 'residual']}},
      varForm: { el: Select, hidden: false, default: 'cumulative', label: 'Form', props: {options: ['individual', 'cumulative']}},
      showLabels: { el: Switch, hidden: false, default: false, label: 'Labels', props: {options: ['hide', 'show']}},
   };

   return controls;
}

export function getPlotFoMControls(res, nresp) {

   const resIDs = Object.keys(res);
   const options = resIDs.length > 1 ? [...resIDs, 'both'] : resIDs;
   const foms = ['rmse', 'r2', 'slope'];
   const defaultOption = resIDs.length > 1 ? 'both' : resIDs[0];


   const controls = {
      resID: { el: Select, hidden: resIDs.length == 1, default: defaultOption, label: 'Show', props: {options: options}},
      fom: { el: Select, hidden: false, default: 'rmse', label: 'FoM', props: {options: foms}},
      resp: { el: RangeDiscrete, hidden: nresp < 2, default: 1, label: 'Response', props: {min: 1, max: nresp}},
      showLabels: { el: Switch, hidden: false, default: false, label: 'Labels', props: {options: ['hide', 'show']}},
   };

   return controls;
}

