   {#snippet info()}
   <h2>Partial Least Squares regression</h2>
   <p>
      Interactive web-application for creating, testing and applying PLS models for multivariate regression
      directly in your browser. The app does not send any information to the server,
      all calculations are done locally on your device.
   </p>
   <p>
      The application uses <a href="https://doi.org/10.1016/j.aca.2023.341096">Procrustes cross-validation</a> for optimization and quality check on the training stage.
   </p>
   <p>Check out YouTube <a href="https://www.youtube.com/playlist?list=PLep7KM9cEyFlyTjYzT1qHeVdhXTe1zKux">video-lessons</a> for more details. The lessons are based on the Tecator dataset, which
   you can download from the app. These data are recorded on a Tecator Infratec Food and Feed Analyzer working in the wavelength
   range 8501050 nm by the Near Infrared Transmission (NIT) principle. The response variable is Fat content (original
   dataset also contains moisture and protein content).
