<script>
   /*************************************************************/
   /* PLSModelCalculator                                        */
   /* -------------------------                                 */
   /* component for training a PLS model                        */
   /*************************************************************/

   import { Switch, Container, Button, Spinner, ButtonCancel, Range } from 'svelte-controls-basic';
   import { app_pls_predict, genhash } from '../../../shared/utils.js';
   import { pls_fit } from '../../../shared/wasm/dist/pls';


   let {
      WASM,                      // promise with WASM methods
      dataset,                   // object with training set
      outliers = [],             // array with dataset row numbers (from 0) marked as outliers
      exclvars = [],             // array with predictor numbers (from 0) markred as excluded
      resobj = $bindable(),      // object with PLS results
      model = $bindable(),       // object with PLS model
      downloadModel = null,      // optional callback to download model object
      downloadResults = null     // optional callback to download results
   } = $props();

   // error handling parameetrs
   let status = $state('neutral');
   let errMsg = $state('');

   // model calculation parameters
   let ncomp = $state(1);
   let center = $state(true);
   let scale = $state(false);

   // promise for model calculation method
   let promise = $state();

   /**
    * Fit PLS model and apply it to training set.
    * @param {Object} dataset - object with training set.
    * @param {Number} ncomp - number of PCs in the model.
    * @param {boolean} center - mean center data columns or not.
    * @param {boolean} scale - standardize data columns or not.
    *
    * Method does not return anything but assign values to the global parameters "model" and "res".
    */
   async function computeModel(dataset, ncomp, center, scale) {
      const nresp = dataset.nresp;
      const varindices = dataset.varindices;

      promise = WASM()
         .then(Module => {
            status = 'computing';
            // fit model and add extra parameters
            const m = pls_fit(Module, dataset.values, nresp, ncomp, center, scale, outliers, exclvars);
            m['hash'] = genhash();                          // unique hash number to trace model updates

            // here we remove labels, values and indices for excluded predictors
            m['varlabels'] = dataset.varlabels.filter((v, i) => i < nresp || !exclvars.includes(varindices[i]));
            m['varindices'] = dataset.varindices.filter((v, i) => i < nresp || !exclvars.includes(varindices[i]));
            m['varvalues'] = dataset.varvalues.filter((v, i) => i < nresp || !exclvars.includes(varindices[i]));

            // these ones needed for plots
            m['xvarlabels'] = m['varlabels'].filter((v, i) => (i + 1) > nresp);
            m['xvarvalues'] = m['varvalues'].filter((v, i) => (i + 1) > nresp);
            m['xvarindices'] = m['varindices'].filter((v, i) => (i + 1) > nresp).map(v => v + 1);

            m['varvaluesName'] = dataset.varvaluesName;    // title for variable axis
            m['varvaluesUnits'] = dataset.varvaluesUnits;  // units if variales are represented by numeric values, e.g. nm
            m['varrev'] = dataset.varrev                   // if variables must be reversed
            m['prep'] = null;                              // array with preprocessing methods will be added when model is downloaded

            // make predictions without preprocessing model
            // as training data is already preprocessed
            const r = app_pls_predict(Module, m, dataset, outliers);

            // add explained variance to model
            m['stat'] = {
               xexpvar: r.stat.xexpvar,
               yexpvar: r.stat.yexpvar,
            }

            // set global variables and status of the component
            model = m;
            resobj = r;
            status = 'success';
         })
         .catch(e =>{
            // something went wrong
            console.error(e);
            model = null;
            status = 'error';
            errMsg = e;
         });
   }

   /**
    * Event handler for computing the model
    * @param e  event object.
    */
   function compute(e) {
      status = 'neutral';
      errMsg = null;
      computeModel(dataset, ncomp, center, scale);
   }

   /**
    * Resets the component status.
    */
   function reset() {
      status = 'neutral';
      promise = null;
      resobj = null;
      model = null;
      errMsg = null;
   }

   // set initial number of components depending on dataset size and manual limit of A = 20
   let maxNComp = $derived(dataset ? Math.min(dataset.values.nrows - 2 - outliers.length,
      dataset.values.ncols - dataset.nresp - exclvars.length - 1, 50) : null);

   // if data file is empty reset status and error messages
   $effect(() => {
      if (dataset === null) reset();
   })

   // reactive effect to set initial value of ncomp
   $effect(() => {
      ncomp = maxNComp ? Math.min(maxNComp, 20) : null;
   });
</script>

{#if (dataset && !model) && status !== 'computing'}
   <!-- settings for global model calculation -->
   {#if ncomp && maxNComp}
   <Container name="ncomp" label="# of LVs">
      <Range id="ncomp" bind:value={ncomp} min={1} max={maxNComp} step={1} decNum={0} />
   </Container>
   {/if}
   <Container name="center" label="Center">
      <Switch id="center" bind:value={center} options={["no", "yes"]} />
   </Container>
   <Container name="scale" label="Scale">
      <Switch id="scale" bind:value={scale} options={["no", "yes"]} />
   </Container>
   <Container name="create-model" label=" ">
      <Button onclick={compute} text="Create model" />
   </Container>
{/if}


{#if promise}

   <!-- calculation is in process -->
   {#await promise}
      <Container name="spinner">
         <Spinner />
         <p>Computing global model...</p>
      </Container>

   {:catch error}
      <!-- error message if failed -->
      <Container name="error" status="error">
         <p>{error}</p>
      </Container>
   {/await}

{/if}

{#if model && model.ncomp }
<Container name="info">
   <p><span style="color:#a0a0a0;">PLS model:</span> {model.ncomp} LVs{model.center ? ', centered' : ''}{model.scale ? ', scaled' : ''}</p>
   <ButtonCancel onclick={reset} />
</Container>
{#if downloadModel && downloadResults}
<Container name="download-buttons" label='Download'>
   <div class="buttons">
   <Button onclick={() => downloadResults(resobj, dataset, "train", outliers)} text="Results" />
   <Button onclick={() => downloadModel(model)} text="Model" />
   </div>
</Container>
{/if}
{/if}


<!-- error message -->
{#if status === 'error' || errMsg}
<Container name="error" status="error">
   <p>{errMsg}</p>
</Container>
{/if}

