<script>
   /**************************************************************/
   /* PCVCalculator                                              */
   /* -------------------------                                  */
   /* component for computing a PV-set                           */
   /**************************************************************/

   import { Select, Container, Button, Spinner, ButtonCancel, ButtonDownload } from 'svelte-controls-basic';
   import { pcvpls } from '../../../shared/wasm/dist/pls.js';
   import { downloadData, app_pls_predict } from '../../../shared/utils.js';

   // main properties of the whole app
   let {
      WASM,
      model,
      calset,
      calres,
      outliers = [],
      pvset = $bindable(),
      pvres = $bindable(),
      pvscalars = $bindable(),
   } = $props();

   // error handling parameetrs
   let status = $state('neutral');
   let errMsg = $state('');

   // PV-set calculation parameters
   let cvtypeOptions = $state(['ven', 'rand', 'loo']);
   let cvType = $state('ven');

   // promise container for calculator
   let promise = $state();

   // compute number of segments options based on dataset size
   let n = calset.values.nrows - outliers.length;
   let nsegOptions = calset && calset.values.nrows > 0 ?
      [3, 4, 5, 10, 20].filter(v =>
         v >= (n / (n - model.ncomp - 1)) && v <= n / 2
      ) : [];

   // select first available option for number of segments as initial
   let nseg = $state(nsegOptions.length > 0 ? nsegOptions[0] : 0);

   // if no options available (dataset is too small) - limit CV choice by LOO
   $effect(() => {
      if (nsegOptions.length === 0) {
         cvtypeOptions = ['loo'];
         cvType = 'loo';
      }
   });

   // function that computes PV-set using given parameters
   async function computePVSet(calset, model, cv, outliers) {
      promise = WASM()
         .then(Module => {
            const cv_type = cv.type == 'loo' ? 0 : cv.type == 'rand' ? 2 : 1;
            const [pvvalues, scalars] = pcvpls(Module, model, calset.values, calset.nresp, model.ncomp, cv.nseg, cv_type, 0, outliers);
            pvset = {

               classLabelsVarname: calset.classLabelsVarname,
               classNames: calset.classNames,
               classes: calset.classes,
               dec: calset.dec,
               fileName: calset.fileName + '-pcv',
               hasClassNames: calset.hasClassNames,
               hasLabels: calset.hasLabels,
               header: calset.header,
               labelsVarname: calset.labelsVarname,

               objindices: calres.objindices,
               objlabels: calres.objlabels,
               sep: calset.sep,
               values: pvvalues,
               varAxisTitle: calset.varAxisTitle,

               varindices: model.varindices,
               varlabels: model.varlabels,
               varrev: model.varrev,
               varvalues: model.varvalues,

               varvaluesName: model.varvaluesName,
               varvaluesUnits: model.varvaluesUnits,
               nresp: model.nresp
            }
            // pv-set is created for outliers free data, hence outliers is empty here
            pvres = app_pls_predict(Module, model, pvset, []);
            pvscalars = scalars;
         })
         .catch((e) => {
            console.error(e)
            pvset = null;
            pvres = null;
            status = 'error';
            errMsg = e;
         });
   }

   // event handler which triggers the model computation
   function compute(e) {
      status = 'neutral';
      errMsg = '';
      computePVSet(calset, model, {type: cvType, nseg: nseg}, outliers);
   }

   // function which resets the state of the component
   function reset() {
      status = 'neutral';
      errMsg = '';
      promise = null;
      pvset = null;
      pvres = null;
   }

   // if data file is empty reset status and error messages
   $effect(() => {
      if (model === null) reset();
   })
</script>


{#if (model && !pvset) && status !== 'busy'}
   <Container name="split" label='Split type:'>
      <Select id='cvType' bind:value={cvType} options={cvtypeOptions} />
   </Container>

   {#if cvType !== 'loo'}
   <Container name="nseg" label='Segments:'>
      <Select id='nseg' bind:value={nseg} options={nsegOptions} />
   </Container>
   {/if}

   <Container name="run-button" label=' '>
      <Button onclick={compute} text="Compute PV-set" />
   </Container>
{/if}

{#if promise}

   {#await promise}
      <Container name="spinner">
         <Spinner />
         <p>Computing PV-set...</p>
      </Container>
   {:catch error}
      <Container name="error" status="error">
         <p>{error}</p>
      </Container>
   {/await}

{/if}

{#if pvset }
<Container name="info">
   <p>
      <span>PV-set:</span>
      {cvType}
      {cvType !== 'loo' ? nseg + ' seg' : ''}<br>
      <span>Size:</span>
      {pvset.values.nrows}&times;{pvset.values.ncols - pvset.nresp} <span>(X)</span>,
      {pvset.values.nrows}&times;{pvset.nresp} <span>(Y)</span>,
   </p>
   <ButtonDownload onclick={() => downloadData(pvset, calset.fileName + '-pv')} />
   <ButtonCancel onclick={reset} />
</Container>
{/if}

{#if status === 'error' && errMsg}
   <Container name="error" status='error'>
      <p>{errMsg}</p>
   </Container>
{/if}

<style>
   p {
      line-height: 1.5em;
   }

   span {
      color:#a0a0a0;
   }
</style>
